<template>
<div class="box">
    <div class="echarts-box m-t-20">
        <img src="../../../assets/img/title/cy-fb.png"/>
        <div class="echarts-height">
            <echarts-bar ids="lineFb" ref="barFb" :data="barDatas"></echarts-bar>
        </div>
    </div>
    <div class="echarts-box">
        <img src="../../../assets/img/title/cy-pt.png" @click="loadChanYePeiTao"/>
        <div class="echarts-height">
            <echarts-bar ids="lineFb2" ref="barFb2" :data="barDatas2"></echarts-bar>

        </div>
    </div>
    <div class="echarts-box">
        <img src="../../../assets/img/title/cy-zsqk.png"/>
        <div class="echarts-height">
            <echarts-bar ids="lineFb3" ref="barFb3" :data="barDatas3"></echarts-bar>
        </div>
    </div>
</div>
</template>

<script>
import echartsBar from '@/components/module/echarts/echartBar.vue'
export default {
    components: {
        echartsBar
    },
    data() {
        return {
            barDatas: {
                title: "",
                data: [342, 786, 288, 355, 256, 156],
                dataAxis: ['金融', 'IT', '服务', '教育', '硬件', '旅游'],
                unit: '家'
            },
            barDatas2: {
                title: "",
                data: [186, 86, 42, 30, 8, 12],
                dataAxis: ['餐厅', '超市', '酒店', '运动', '银行', '诊所'],
                unit: '家'
            },
            barDatas3: {
                title: "",
                data: [62, 74, 78, 86, 90, 95],
                dataAxis: ['2012', '2013', '2014', '2015', '2016', '2017'],
                unit: '%'
            },
            colorLists: ['#0084ff', "#0084ff"],
        }
    },
    methods: {
        loadChanYePeiTao() {
            if (this.$parent.$parent.$parent.$parent.$refs.map) {
                this.$parent.$parent.$parent.$parent.$refs.map.loadChanYePeiTaoIcon();
            }
        }
    }
}
</script>

<style lang="less" scoped>
.box {
    color: #fff;
    margin-right: 40px;
    margin-top: 152px;
    width: 400px;
    float: right;

    & h3 {
        line-height: 1.8em;
    }

    & h3,
    label {
        font-size: 26px;
        font-weight: normal;
    }

    & span {
        font-size: 20px;
    }

    & p {
        margin: 0 0;
        font-size: 20px;
    }

    & big {
        font-size: 48px;
        line-height: 50px;
        display: inline-block;
        letter-spacing: -3px;
        padding-right: 10px;
    }

    & small {
        font-size: 16px;
        display: inline-block;
        text-align: right;
    }
}

.m-t-20 {
    margin-top: 20px;
}

.echarts-height {
    height: 240px;
}
</style>



