<template>
  <div id="app">
    <router-view ref="rootView"/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted(){
    var proxy = this.$proxy;
	  setTimeout(() => {
		 proxy.on("routerTo", data => {
            this.routerTo(data);
		});
		 proxy.on("routerToSub", data => {
            this.routerToSub(data);
        });
	  }, 800);
  },
  methods:{
     routerTo(path) {
  		if(this.$refs.rootView.routerTo){
        this.$refs.rootView.routerTo(path);
      }
    },
    routerToSub(path) {
  		if(this.$refs.rootView.routerToSub){
        this.$refs.rootView.routerToSub(path);
      }
    },
    
  }
}
</script>

<style>
#app {
  font-family:"微软雅黑" ,"microsoft yahei",'Avenir', Helvetica, Arial, sans-serif;
  position: absolute;
  /*width: 3840px;*/
  width: 100%;
 
  height: 1080px;
  background: #0e122b;
}
::-webkit-scrollbar{
	height: 0;
	width: 18px;
}
html,body,p,h1,h2,h3,h4,h5,*{
	margin: 0;
	padding: 0;
}
</style>
<style lang="less">
	@import url("assets/less/style.less");
</style>


