<template>
	<div class="company">
		<img src="../../../assets/img/title/cy-nsbh.png"/>
		<div class="echartline" >

			<myBar ids="bar2" ref="bar2" :data="barDatas"></myBar>
		</div>
		<img src="../../../assets/img/title/cy-rygc.png"/>
		<div class="echartline" >
			<myLine ids="line2" ref="line2" :data="lineData1"></myLine>
		</div>
		<img src="../../../assets/img/title/cy-zscq.png"/>
		<div class="echartline" >

			<myLine ids="line3" ref="line3" :data="lineData"></myLine>
		</div>
	</div>
</template>

<script>
	import myLine from '@/components/module/echarts/line.vue'
	import myBar from '@/components/module/echarts/echartBar.vue'
	export default{
		components:{
			myLine,
			myBar
		},
		data(){
			return{
				lineData:{
					data:[40,98,55,75],
					dataAxis:['2015','2016','2017','2018'],
					unit:'(个)'
				},
				lineData1:{
					data:[440,498,655,575,325,215,121],
					dataAxis:['博士','硕士','学士','大专','高中','中专','初中'],
					unit:'(个)'
				},
				barDatas:{
					title:"",
					data:[5,8,4.7,3.3],
					dataAxis:['2015','2016','2017','2018'],
					unit:'万元'
				},
				colorLists:['#0084ff',"#0084ff"],
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-top: 152px;
		margin-right: 40px;
		float: right;
		width: 400px;;
		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& small{
			color: rgba(255,255,255,.4);
			font-size: 20px;
		}
		& h3,label{
			font-size: 24px;
		}
	}
	.echartline{
		height: 240px;
		width: 100%;
	}
</style>


