<template>
	<div style="position: absolute;width: 100%;height: 100%;background: rgba(0,0,0,.7);">
		<canvas id="canvasNumber" width="1920" height="979"></canvas>
	</div>
</template>

<script>
	export default{
		methods:{
			initNumber(){
				var Canvas = {};
					Canvas.anim = {
					    //初始化
					    init: function() {
					        var canvas = document.getElementById('canvasNumber');
					        canvas.width = window.innerWidth/2;
					        canvas.height = window.innerHeight;
					        this.ctx = canvas.getContext('2d');
					        this.cw = canvas.width;
					        this.ch = canvas.height;
					        this.particles = [];
					        this.letters = '010101';
					    },
					    //执行动画
					    render: function() {
					        //显示位置 
					        var particle = {
					            x: Math.random() * 1000,
					            y: this.ch-200,
					            character: this.letters[Math.floor(Math.random() * this.letters.length)],
					            xSpeed:0,
					            ySpeed: 0,
					            color: [155, 100, 50, .7]
					        }
					        this.particles.push(particle);
					        this.drawparticles();
					    },
					    //绘制字母
					    drawparticles: function() {
					        this.fadeCanvas()
					        var particleCount = this.particles.length;
					        for (var i = 0; i < particleCount; i++) {
					            var particle = this.particles[i];
					            var h = particle.color[0],
					                s = particle.color[1] + '%',
					                l = particle.color[2] + '%',
					                a = particle.color[3];
					            var hsla = `hsla(${h},${s},${l},${a})`
					            this.ctx.font = '24px sans-serif';
					            this.ctx.fillStyle = hsla; //字体颜色
					
					            this.ctx.fillText(particle.character, particle.x, particle.y); //字体属性
//					            particle.x += particle.xSpeed
//					            particle.y += particle.ySpeed
					            particle.y *= 0.98;
					            particle.color[2] *= 0.99;
					            particle.color[0] += 1;
					            if (particle.color[0] > 253) {
					                particle.color[2] = 0;
					            }
					        }
					    },
					    //先清除画布
					    fadeCanvas: function() {
					        this.ctx.fillStyle = 'rgba(0,0,0,.7)'
					        this.ctx.fillRect(0, 0, this.cw, this.ch);
					    }
					}
					Canvas.anim.init();
					Canvas.anim.render();
					
					var i=setInterval(function() {
					    Canvas.anim.render();
					},60)
					window.onresize = function() {
					    Canvas.anim.init();
					}
			}
		},
		mounted(){
			this.$nextTick(()=>{
				this.initNumber();
				
			})

		}
	}
</script>

<style>
	#canvasNumber{
		position: absolute;
		width: 50%;
		left: 25%;
		height: 100%;
		opacity: .7;
	}
</style>


