				<div class="echartsleft">
					<p>网站当日访问量<span style="float:right;">单位:%</span></p>
					<div class="echartsline" style="height: 300px;">
						<lines ids="line" ref="line" :data="lineData1"></lines>
					</div>
				</div>
				<img src="../assets/img/pptg/bdsssj.png" style="margin-top:25px;"/>
