		background: linear-gradient(to top, #0072ff, #01e9ff);
		-webkit-background-clip: text;
		color: transparent;
		font-size:20px;
		font-weight:550;
	}
	.my-table{
		display: table;/*作为块级表格来显示 */
		width: 100%;
		color:#FFFFFF;
		padding:0;
		margin:0;
		& p{
			background: linear-gradient(to top, #0072ff, #01e9ff);
			-webkit-background-clip: text;
			color: transparent;
			font-size:20px;
			font-weight:550;
		}
		}
	.my-table ul{
			padding:0;
			margin:0;
		}
	.my-table li{
		box-sizing: border-box;
		display:table;
		width: 390px;
		height:39px;
		color: #FFFFFF;
		list-style: none;
	}
	label{
		padding: 0 5px;
		display: table-cell;
	}	
	.table-title{
		color: #FFFFFF;
		line-height:40px;
		display: table;
		width: 100%;
		background: rgba(0,85,189,.53);	
	}		
	.table-content li{
		padding-bottom: 6px;
		padding-top: 7px;
		border-bottom: 1px solid rgba(255,255,255,.24);
	}
	.order{
		display:inline-block;
		width:30px;
		height:26px;
		& span{
			margin-left:5px;
		}	
	}
	.orderfirst{
