	}
	.ordersecond{
		background:url('../assets/img/pptg/paiming2.png') center center no-repeat;
	}	
  .orderthird{
		background:url('../assets/img/pptg/paiming3.png') center center no-repeat;
	}
	.orderother{
		background:url('../assets/img/pptg/paiming4.png') center center no-repeat;
	}
	.publicdata p{
		background: linear-gradient(to top, #0072ff, #01e9ff);
		-webkit-background-clip: text;
		color: transparent;
		font-size:20px;
		font-weight:550;
	}
</style>


