<template>
	<div class="park-total">
		<img src="../../../assets/img/title/park-traffic.png"/>
		<div class="CarTotal">
			<percent-pie ids="myCar" :data="carTotal"></percent-pie>
		</div>
		<ul class="carText">
			<li v-for="(item,index) in carTotal" :key="index"><p>{{item.name}}</p><p><big>{{item.value}}</big><small>个</small></p></li>
		</ul>

		<img src="../../../assets/img/title/park-hxxt.png"/>
		<ul style="margin-top: 30px;">
			<li v-for="(item,index) in indexList" :key="index">
				<!--<i></i>-->
				<p>{{item.title}}</p>
				<p><big>{{item.data}}</big><small>{{item.unit}}</small></p>
			</li>
		</ul>
	</div>
</template>

<script>
	import percentPie from './../echarts/percentPie'
	export default{
		components:{
			percentPie
		},
		data(){
			return{
				indexList:[{
					title:'单日用水量',
					icon:'icon-water',
					unit:'M³',
					data:60
				},{
					title:'单日用电量',
					icon:'icon-electricity',
					unit:'M³',
					data:600
				},{
					title:'单日用气量',
					icon:'icon-gas',
					unit:'NM³',
					data:600
				},{
					title:'消防报警',
					icon:'icon-fire',
					unit:'次',
					data:6
				},{
					title:'敏感人群',
					icon:'icon-sensitive',
					unit:'个',
					data:6
				},{
					title:'电梯运行情况',
					icon:'icon-elevator small',
					unit:'',
					data:'正常'
				}],
				carTotal:[{
				    'name': '总停车位',
				    'value': 524
				}, {
				    'name': '已使用车位',
				    'value': 339
				}, {
				    'name': '剩余车位',
				    'value': 185
				}],
			}
		}
	}
</script>
<style scoped="scoped" lang="less">

	.park-total li{
	    width: 50%;
	    display: inline-block;
	    margin-left: -4px;
	    box-sizing: border-box;
	    padding-left: 0;
	}
	.park-total li:last-of-type big{
		font-size: 36px;
	}
	.CarTotal{
		margin: 30px 0 50px;
	}
	.carText{
		display: inline-block;
		width: 145px;
		padding-left: 40px;
		box-sizing: border-box;
	 	li{
	        list-style: none;
	        text-align: left;
	        position: relative;
	        width: 100%;
	        margin-bottom: 20px;
	        p{
	            margin: 0;
	            font-size:18px;
	            color: #FFFFFF;
	        }
	        big{
	            background: linear-gradient(to top,#0072ff, #00c0ff);
	            -webkit-background-clip: text;
	            color: transparent;
	            font-size: 36px;
	            line-height: 40px;
	            font-family: "DINOT";   
	             font-weight: bold;
	        }
	        small{
	            font-size: 16px;
	            padding-left: 10px;
	            color:#a8a8a8;
	        }
	        
	    }
	}
</style>


