const HUBNAME = 'DefaultHub';
var mysignalr={
    startConnection:function(url,hubname){
        var conn=$.hubConnection(url)
        var proxy = conn.createHubProxy(hubname);
	    proxy.on("addMessage", data => {
            console.log(data+"333");
        }); //定义客户端方法
        conn.start().done((connection) => {
            console.log('Now connected, connection ID=' + connection.id)
            // proxy.invoke('send', ''); 调用服务端方法
        }).fail((data) => {
           console.log('conn fail')
        });
        conn.disconnected(function () {
            console.log('disconnected')
        });
        return proxy;
    }
}

export default mysignalr;



