<template>
	<div class="company">
		<div class="pici">
			<p>当前批次</p>
			<el-row :gutter="10">
				<el-col :span="6">
					<div class="pici-border active">第一批次</div>
				</el-col>
				<el-col :span="6">
					<div class="pici-border">第二批次</div>
				</el-col>
				<el-col :span="6">
					<div class="pici-border">第三批次</div>
				</el-col>
				<el-col :span="6">
					<div class="pici-border">第四批次</div>
				</el-col>
			</el-row>
		</div>
		<div class="comTotal" >
			<div class="list-li">
				<i class="small-icon icon-house"></i>
				<div class="card">
					<p>企业总数</p>
					<p class="color-gradient"><big class="color-gradient">254</big>家</p>
				</div>
			</div>
			<div class="list-li">
				<i class="small-icon icon-user"></i>
				<div class="card">
					<p>员工总数</p>
					<p class="color-gradient"><big class="color-gradient">8416</big></p>
				</div>
			</div>
			<div class="list-li">
				<i class="small-icon icon-qiye"></i>
				<div class="card">
					<p>世界500强企业</p>
					<p class="color-gradient"><big class="color-gradient">16</big>家</p>
				</div>
			</div>
			<div class="list-li">
				<i class="small-icon icon-djs"></i>
				<div class="card">
					<p>独角兽企业</p>
					<p class="color-gradient"><big class="color-gradient">12</big>家</p>
				</div>
			</div>
			<div class="list-li">
				<i class="small-icon icon-djs"></i>
				<div class="card">
					<p>潜在独角兽企业</p>
					<p class="color-gradient"><big class="color-gradient">20</big>家</p>
				</div>
			</div>
			<div class="list-li">
				<i class="small-icon icon-jingji"></i>
				<div class="card">
					<p>新经济重点企业</p>
					<p class="color-gradient"><big class="color-gradient">12</big>家</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import myBar from '@/components/module/echarts/bar.vue'
	export default{
		components:{
			myBar
		},
		props:['comData'],
		data(){
			return{
				barDatas:{
					title:"",
					data:[811,781,954,845,541],
					dataAxis:['第一批次','第一批次','第一批次','第一批次'],
					unit:''
				},
				colorLists:['#0084ff',"#0084ff"],
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-left: 40px;
		width: 390px;
		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& h3,label{
			font-size: 24px;
		}
		& span{
			font-size: 20px;
		}
		& p{
			margin: 20px 0;
		}
		& .buildMsg{
			display: table;width: 100%;
			padding: 10px 0;
			border-bottom: 1px solid rgba(255,255,255,.2);
			 span{

				display: table-cell;
			}
			 .Ware{
				width: 7em;
				display: table-cell;
				 cite{
					font-style: normal;
				}
			}
			.bor{
				border: 1px solid #fff;
				border-radius: 20px;
				padding: 4px 20px;
				cursor: pointer;
				cite{
					margin-right: 15px;
				}
				&:hover{
					background: linear-gradient(to top, #00c0ff, #0090ff);
			        -webkit-background-clip: text;
			        color: transparent;
					border-color:#0090FF
				}
			}
			
		}
		
	}
	.comTotal{
		margin-top: 80px;
		& p{
			font-size: 24px;
		}
	}
	.total{
		 background: linear-gradient(to top, #00c0ff, #0090ff);
        -webkit-background-clip: text;
        color: transparent;
        font-size: 20px;
        & big{
        	font-size: 48px;
        	margin-right: 20px;
        }
	}
	.echartline{
		height: 250px;
		width: 480px;
	}
	
	
	.pici{
		p{
			font-size: 30px;
		}
		.pici-border{
			border-radius: 20px;
			/*border: 2px solid #FFFFFF;*/
			padding:0 10px;
			text-align: center;
			cursor: pointer;
			position: relative;
			height: 32px;
			line-height: 32px;
		}
		.pici-border.active,.pici-border:hover{
		    background: -webkit-gradient(linear, 0 0, 0 100%, from(#f57a02), to(#ffc400));
		    background: -webkit-linear-gradient(to top, #f57a02, #ffc400);
		    background: -moz-linear-gradient(to top, #f57a02, #ffc400);
		    background: -o-linear-gradient(to top, #f57a02, #ffc400);
		    background: linear-gradient(to top, #f57a02, #ffc400);
		    -webkit-background-clip: text;
		    color: transparent;
		}
		.pici-border:after{
			content: "";
		    width: 100%;
		    height: 100%;
		    top:0;
		    left: 0;
		    display: block;
		    z-index: -1;
		    position: absolute;
		   background: url(../../../assets/img/border-w.png)no-repeat;
		    background-size: 100% 100%;
		}
		.pici-border.active:after,
		.pici-border:hover::after{
			background: url(../../../assets/img/border.png)no-repeat;
		    background-size: 100% 100%;
		   
		}
	}
	.list-li{
		margin-bottom: 45px;
		.card{
   			vertical-align: bottom;
			display: inline-block;
			p{
				font-size: 16px;
				margin: 0;
			}
			big{
			    height: 20px;
			    font-size: 48px;
			    overflow: hidden;
			    line-height: 48px;
			    margin-right: 10px;
			}
		}
		.small-icon{
			margin-right: 15px;
		}
		.color-gradient{
			line-height: 40px;
			height: 45px;
		}
	}
</style>


