<template>
	<div style="height:100%;" :id="ids">

	</div>
</template>

<script>
	export default{
		props:{
			ids:{
				type:String,
				default:"bar"
			},
			data:{
				default:{
					title:"",
					data:[],
					dataAxis:[]
				}
			},
			color:{
				default:[]
			}
		},
		data(){
			return{
				
			}
		},
		methods:{
			setEcharts( data, dataAxis, myChart, color,unit) {
				let echarts=this.$echarts;
				var dataShadow = [];
				var option = {
					title: {
						show:false,
					},
					tooltip: {
						trigger: 'item',
				        // axisPointer : {            // 坐标轴指示器，坐标轴触发有效
				        //     type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
				        // }
					},
					grid: {
						left: 60,
						right: 0,
						top: '40px',
						bottom: "50px",
						show: false
					},
					xAxis: {
						data: dataAxis,
						axisLabel: {
							textStyle: {
								color: '#fff',

							},
						},
						axisTick: {
							show: false
						},
						z: 10,
						axisLabel:{
							color:'#fff',
							fontSize:18,
							interval:0
						},
						axisLine: {
							lineStyle:{
								color:'rgba(255,255,255,.05)'
							}
						}
					},
					yAxis: {
						splitNumber:3,
				        nameGap:30,
						axisTick: {
							show: false
						},
						axisLabel: {
							show: true,
							textStyle: {
								color: '#fff',
								fontSize:20
							}
						},


						nameTextStyle:{
							color:'#fff',
							fontSize:20
						},
						name:unit,
						splitLine: {
							show: false,
						},
						axisLine:{
							show:false
						}
						
					},
					dataZoom: [{
						type: 'inside'
					}],
					series: [
						{
							type: 'bar',
							barGap: '-100%',
							barWidth:20,
							itemStyle: {
								normal: {
									color: new echarts.graphic.LinearGradient(
										0, 0, 0, 1, [
											{ offset: 0, color: color[0] },
											{ offset: 1, color: color[1] }
										]
									)
								},
								emphasis: {
									color: new echarts.graphic.LinearGradient(
										0, 0, 0, 1, [
											{ offset: 0, color: color[1] },
											{ offset: 1, color: color[0] }
										]
									)
								}
							},
							data: data
						}
					]
				};
				myChart.setOption(option);
			},
			initBar(){
				let initids=this.$echarts.init(document.getElementById(this.ids));
				this.setEcharts(this.data.data, this.data.dataAxis, initids, this.color,this.data.unit);
			}
		},
		watch:{
			
		},
		created(){
			
		},
		mounted(){
			this.$nextTick(()=>{
				this.initBar();
			})
		},
		destoryed(){
			
		}
	}
</script>

<style>
</style>


