<template>
	<div class="company">
		<div class="echarts-box m-t-20">
			<img src="../../../assets/img/title/cy-zdqy.png"/>
			<div class="build-table">
				<ul class="title">
					<li><span class="table-title">园区企业</span><span>楼层</span></li>
				</ul>
				<ul>
					<li v-for="item in comData"><span class="table-title">{{item.name}}</span><span>{{item.floor}}</span></li>
				</ul>
			</div>
		</div>
		<div class="echarts-box">
			<img src="../../../assets/img/title/cy-zsqk.png"/>	
			<div class="echarts-height">
				<myBar ids="lineFb3" ref="barFb3" :data="barDatas3"></myBar>
			</div>
		</div>
	</div>
</template>

<script>
	import myLine from '@/components/module/echarts/line.vue'
	import myBar from '@/components/module/echarts/echartBar.vue'
	export default{
		components:{
			myLine,
			myBar
		},
		data(){
			return{
				comData:[{
					name:'成都数创联铭品科技有限公司',
					floor:'C-654'
				},{
					name:'成都智元汇信息科技股份有限公司',
					floor:'C-654'
				},{
					name:'成都二十三魔方生物科技有限公司',
					floor:'C-654'
				},{
					name:'成都市极米科技有限公司',
					floor:'C-654'
				},{
					name:'成都先导药物开发有限公司',
					floor:'C-654'
				},{
					name:'货车帮(成都运力科技有限公司)',
					floor:'C-654'
				},{
					name:'咕咚(成都乐动信息技术有限公司)',
					floor:'C-654'
				},{
					name:'医联(成都医云科技有限公司)',
					floor:'C-654'
				},{
					name:'Testbird(成都中云天下科技有限公司)',
					floor:'C-654'
				}],
				lineData:{
					data:[40,98,55,75],
					dataAxis:['2015','2016','2017','2018'],
					unit:'(个)'
				},
				lineData1:{
					data:[440,498,655,575,325,215,121],
					dataAxis:['2014','2015','2016','2017','2018'],
					unit:'(个)'
				},
				barDatas:{
					title:"",
					data:[15,15,18,14.7,13.3],
					dataAxis:['2014','2015','2016','2017','2018'],
					unit:'家'
				},
				barDatas3:{
					title:"",
					data:[62,74,78,86,90,95],
					dataAxis:['2012','2013','2014','2015','2016','2017'],
					unit:'%'
				},
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-top: 152px;
		margin-right: 40px;
		float: right;
		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& small{
			color: rgba(255,255,255,.4);
			font-size: 20px;
		}
		& h3,label{
			font-size: 24px;
		}
	}
	.comTotal{
		margin-top: 40px;
		& p{
			font-size: 24px;
		}
	}
	.total{
		 background: linear-gradient(to top, #00c0ff, #0090ff);
        -webkit-background-clip: text;
        color: transparent;
        font-size: 20px;
        & big{
        	font-size: 48px;
        	margin-right: 20px;
        }
	}
	.echartline{
		height: 280px;
		width: 480px;
	}
	
	.echarts-height{
		height: 240px;
	}
	.build-table{
		margin-bottom: 30px;
		ul.title{
			background: rgba(0,85,189,.53);
			margin-top: 30px;
		}
		ul{
			display: table;
			width: 100%;
			li{
				display: table-row;
				span{
					padding: 0 10px;
					box-sizing: border-box;
					border-bottom: 1px solid rgba(255,255,255,.2);
					line-height: 3em;
					display: table-cell;
				}
				.table-title{
					width: 320px;
				}
			}
		}
		
	}
</style>


