<template>
	<div class="company">
		<div class="echarts-box m-t-20">
			<img src="../../../assets/img/title/cy-fb.png"/>
			<div class="echarts-height">
				<myBar ids="lineFb" ref="barFb" :data="barDatas"></myBar>
			</div>
		</div>
		<div class="echarts-box">
			<img src="../../../assets/img/title/cy-qyrz.png"/>	
			<div class="echarts-height">
				<myLine ids="line2" ref="line2" :data="lineData1"></myLine>
			</div>
		</div>
		<div class="echarts-box">
			<img src="../../../assets/img/title/cy-zsqk.png"/>	
			<div class="echarts-height">
				<myBar ids="lineFb3" ref="barFb3" :data="barDatas3"></myBar>
			</div>
		</div>
	</div>
</template>

<script>
	import myLine from '@/components/module/echarts/line.vue'
	import myBar from '@/components/module/echarts/echartBar.vue'
	export default{
		components:{
			myLine,
			myBar
		},
		data(){
			return{
				lineData:{
					data:[40,98,55,75],
					dataAxis:['2015','2016','2017','2018'],
					unit:'(个)'
				},
				lineData1:{
					data:[440,498,655,575,325,215,121],
					dataAxis:['2014','2015','2016','2017','2018'],
					unit:'(个)'
				},
				barDatas:{
					title:"",
					data:[15,15,18,14.7,13.3],
					dataAxis:['2014','2015','2016','2017','2018'],
					unit:'家'
				},
				barDatas3:{
					title:"",
					data:[62,74,78,86,90,95],
					dataAxis:['2012','2013','2014','2015','2016','2017'],
					unit:'%'
				},
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-top: 152px;
		margin-right: 40px;
		float: right;
		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& small{
			color: rgba(255,255,255,.4);
			font-size: 20px;
		}
		& h3,label{
			font-size: 24px;
		}
	}
	.comTotal{
		margin-top: 40px;
		& p{
			font-size: 24px;
		}
	}
	.total{
		 background: linear-gradient(to top, #00c0ff, #0090ff);
        -webkit-background-clip: text;
        color: transparent;
        font-size: 20px;
        & big{
        	font-size: 48px;
        	margin-right: 20px;
        }
	}
	.echartline{
		height: 280px;
		width: 480px;
	}
	
	.echarts-height{
		height: 240px;
	}
</style>


