<template>
	<div class="right-park-total right-park">
		<img src="../../../assets/img/title/park-pay.png"/>
		<ul>
			<li v-for="(item,index) in carTotal" :key="index"><p>{{item.name}}</p><p><big>{{item.value}}</big><small>万元</small></p></li>
		</ul>
		<img src="../../../assets/img/title/park-yjxt.png"/>
		<div class="warning">
			<div class="warn-list" :class="item.icon" v-for="(item,index) in warnData" :key="index">
				<label>{{item.title}}</label>
				<span>{{item.status}}</span>
			</div>
		</div>
	</div>
	
</template>

<script>
	import percentPie from './../echarts/percentPie'
	export default{
		components:{
			percentPie
		},
		data(){
			return{
				carTotal:[{
				    'name': '今日总充值',
				    'value': 524
				}, {
				    'name': '今日总消费',
				    'value': 339
				}, {
				    'name': '今年累计充值',
				    'value': 185
				}, {
				    'name': '今年累计消费',
				    'value': 185
				}],
				warnData:[{
					icon:"icon-user-act",
					title:'人脸识别系统',
					status:'正常'
				},{
					icon:"icon-earth-act",
					title:'地震预警',
					status:'正常'
				},{
					icon:"icon-percen-act",
					title:'人群聚集预警',
					status:'正常'
				},{
					icon:"icon-anfang-act",
					title:'红外线安防预警',
					status:'正常'
				},{
					icon:"icon-elevator-act",
					title:'电梯预警',
					status:'正常'
				},{
					icon:"icon-firesave-act",
					title:'消防预警',
					status:'正常'
				},{
					icon:"icon-watersave-act",
					title:'水电气系统预警',
					status:'正常'
				}]
			}
		}
	}
</script>



