<template>
	<div class="company">
		<div class="comMsg">
			<img src="../../../assets/img/title/cy-qyxq.png"/>
			<p><span>企业名称：</span><label>{{comData.name}}</label></p>
			<p><span>注册资本：</span><label>{{comData.money}}</label></p>
			<p><span>成立日期：</span><label>{{comData.startTime}}</label></p>
			<p><span>经营状态：</span><label>{{comData.type}}</label></p>
			<p><span>信用代码：</span><label>{{comData.code}}</label></p>
			<p><span>经营风险：</span><label>{{comData.risk}}</label></p>
		</div>
		<div class="echartline" style="margin-top: 80px;">
			<img src="../../../assets/img/title/cy-qyys.png"/>
			<myLine ids="line" ref="line" :data="lineData"></myLine>
		</div>
	</div>
</template>

<script>
	import myLine from '@/components/module/echarts/line.vue'
	export default{
		components:{
			myLine
		},
		props:['comData'],
		data(){
			return{
				lineData:{
					data:[25,45,25,40,98,55,75],
					dataAxis:['2012','2013','2014','2015','2016','2017','2018'],
					unit:'(个)'
				}
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-left: 40px;

		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& h3,label{
			font-size: 24px;
			line-height: 2em;
		}
		& span{
			font-size: 20px;
		}
		& p{
			margin: 15px 0;
		}
	}
	.echartline{
		height: 250px;
		width: 480px;
	}
</style>


