<template>
	<div style="height:100%;" :id="ids">

	</div>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		props: {
			ids: {
				default: 'percentPie'
			},
			data: {
				default: [{
					'name': '北京',
					'value': 100,
					'unit':'个'
				}]
			},
			total: {
				default: 100
			},
			colorList:{
				default:function(){return ['#00ccff', '#006cff']}
			}
		},
		methods: {
			initPie(id, data, total) {
				var placeHolderStyle = {
					normal: {
						label: {
							show: false
						},
						labelLine: {
							show: false
						},
						color: "rgba(0,0,0,0)",
						borderWidth: 0
					},
					emphasis: {
						color: "rgba(0,0,0,0)",
						borderWidth: 0
					}
				};

				var dataStyle = {
					normal: {
						formatter: '{c}',
						position: 'center',
						show: true,
						textStyle: {
							fontSize: '36',
							fontWeight:'bold',
				            fontFamily:'DINOT',
							padding:[20,0,0,0],
							color: '#fff'
						}
					}
				};
				let echarts=this.$echarts;
				var color=this.colorList
				var option = {

					title: [{
						text: data[0].unit||'个',
						left: '39.5%',
						top: '70%',
						textAlign: 'center',
						textStyle: {
							fontWeight: 'normal',
							fontSize: '16',
							color: '#fff',
							textAlign: 'center',
						},
						padding:[1,15],
						backgroundColor: 'rgba(0,126,255,.21)',
						borderRadius: 10
					}],
					series: [{
							type: 'pie',
							hoverAnimation: false, //鼠标经过的特效
							radius: ['70%', '78%'],
							center: ['50%', '50%'],
							startAngle: 225,
							labelLine: {
								normal: {
									show: false
								}
							},
							label: {
								normal: {
									position: 'center'
								}
							},
							data: [{
									value: this.data[0].value,
									itemStyle: {
										normal: {
											color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
												offset: 0,
												color: color[0]
											}, {
												offset: 1,
												color:color[1]
											}]),
										}
									},
									label: dataStyle,
								}, {
									value: this.data[0].value/3,
									itemStyle: placeHolderStyle,
								},

							]
						},

						//外圈的边框
						{
							// name: '总人数',
							type: 'pie',
							hoverAnimation: false, //鼠标经过的特效
							radius: ['84%', '87%'],
							center: ['50%', '50%'],
							startAngle: 225,
							labelLine: {
								normal: {
									show: false
								}
							},
							label: {
								normal: {
									position: 'center'
								}
							},
							data: [{
									value: this.data[0].value,
									itemStyle: {
										normal: {
											color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
												offset: 0,
												color: 'rgba(0,204,255,.3)'
											}, {
												offset: 1,
												color: 'rgba(0,108,255,.3)'
											}]),
										}
									},
								}, {
									value: this.data[0].value/3,
									itemStyle: placeHolderStyle,
								},

							]
						},

					]
				};
				let myChart=this.$echarts.init(document.getElementById(this.ids));
				myChart.setOption(option);
			}
		},
		mounted() {
			this.initPie(this.ids, this.data, this.total);
		}
	}
</script>

<style>

</style>


