<template>
<div class="bg">
    <div class="bg" id="cesiumContainer">

    </div>
    <!-- <img src='' id="overlayer" style="position:absolute"> -->
    <!-- <el-collapse-transition> -->

    <!-- <el-header>
      c区停车场
  </el-header> -->

    <div id="overlayer" class="map-wind" v-show="showVideo">
        <img src='/static/img/timg.gif' style="width:100%;height:100%" >
    </div>
        <div id="warnWin" class="map-wind" v-show="showwin">
            <img src="../../assets/img/warn.png"/>
            <el-form label-width="5em">
                <el-form-item label="报警时间"><span>2018.10.24 15:24:56</span></el-form-item>
                <el-form-item label="报警位置"><span>E区3栋8楼</span></el-form-item>
                <el-form-item label="报警原因"><span>发现小偷</span></el-form-item>
            </el-form>
        </div>
        <div id="warnFace" class="map-wind" style="height:230px" v-show="showFace">
            <img src="../../assets/img/face.png"/>
            <div style="text-align: center;">
                <img src="../../assets/img/face-img.jpg"/>
    	</div>
            </div>
 		<div id="warnIframe" class="map-wind win-big" style="height:665px;width: 943px;top:50%;margin-top:-333px;left:50%;margin-left:-472px" v-show="showIframe">
           <iframe :src="qjurl" width="900" height="620"></iframe>
    	</div>
            <!-- </el-collapse-transition> -->
        </div>
</template>

<style scoped>
.bg {
  width: 100%;
  height: 100%;
}
</style>

<script>
import "../../utiljs/arryutil.js";
export default {
  data() {
    return {
      s3murl: "",
      start: "",
      linetest: [],
      tanzhaodengtimer: "",
      tanzhaodeng: "",
      showVideo: false,
      showFace: false,
      showwin: false,
      showIframe:false,
      customDataSources: [],
      qjurl:""
    };
  },
  mounted() {
    this.s3murl = s3murl;
    this.qjurl=qjurl;
    // //注册数组的扩展方法firstOrDefault
    // Array.prototype.firstOrDefault =
    //   Array.prototype.firstOrDefault ||
    //   function(func) {
    //     var arr = this;
    //     var r = null;
    //     for (var i = 0; i < arr.length; i++) {
    //       if (func(arr[i], i, arr)) {
    //         r = arr[i];
    //         break;
    //       }
    //     }
    //     return r;
    //   };
    this.initmap();
  },
  methods: {
    initmap() {
      var that = this;
      this.viewer = new Cesium.Viewer("cesiumContainer", {
        navigation: false,
        infoBox: false,
        selectionIndicator: false,
        timeline: true,
        animation: true,
        // imageryProvider: new Cesium.MapboxImageryProvider({
        //   mapId: "mapbox.dark",
        //   accessToken:
        //     "pk.eyJ1IjoieW91bmdnaXMiLCJhIjoiY2o5Z3owdTViMnR5djJ3bGczaGw4ODdhZSJ9.K7Zmkra18V7CuynSW3rlRQ"
        // }),
        imageryProvider: new Cesium.SingleTileImageryProvider({
          url: "/static/img/world.jpg"
        })
        // imageryProvider: new Cesium.WebMapTileServiceImageryProvider({
        //         url : 'http://172.18.0.27:9009/arctiler/arcgis/services/ags-19/MapServer',
        //         layer : 'AZDBMap',
        //         style : 'default',
        //         format : 'image/png',
        //         tileMatrixSetID : 'AZMap_AZDBMap',
        //         maximumLevel: 16,
        //         credit : new Cesium.Credit('U. S. Geological Survey')
        //     })
      });
      var scene = this.viewer.scene;
      scene.fxaa = true;
      scene.skyAtmosphere.show = false;
      var widget = this.viewer.cesiumWidget;
      var url = this.s3murl;
      this.start = Cesium.JulianDate.fromDate(new Date(2015, 2, 25, 16));
      var stop = Cesium.JulianDate.addSeconds(
        this.start,
        360,
        new Cesium.JulianDate()
      );
      this.viewer.clock.startTime = this.start.clone();
      this.viewer.clock.stopTime = stop.clone();
      this.viewer.clock.currentTime = this.start.clone();
      this.viewer.clock.clockRange = Cesium.ClockRange.LOOP_STOP; //Loop at the end
      this.viewer.clock.multiplier = 10;
      this.viewer.timeline.zoomTo(this.start, stop);
      var promise = scene.addS3MTilesLayerByScp(url[0], {
        name: "S3m"
      });
      scene.addS3MTilesLayerByScp(url[2], {
        name: "road2"
      });
      scene.addS3MTilesLayerByScp(url[3], {
        name: "road3"
      });
      scene.addS3MTilesLayerByScp(url[4], {
        name: "road4"
      });
      scene.addS3MTilesLayerByScp(url[5], {
        name: "road5"
      });
      
      //that.loadchanye();
      //that.loadPiciMian();
      //that.loadQuicon();
      //that.loadAlarmCircle();
      //that.loadDaoza();
      //that.testCircleRaise();
      Cesium.when(promise, layer => {
        //     debugger;
        //    layer.height=500;
        //    var timer= window.setInterval(()=>{
        //         if(layer.height==0){
        //             window.clearInterval(timer);
        //         }
        //     layer.height-=50;
        //     },2000)
        that.goHome();
        // that.testclock();
        layer.selectEnabled = false;
        //layer.visibleDistanceMax =10000
        var promisecqu = scene.addS3MTilesLayerByScp(that.s3murl[1], {
          name: "cqu"
        });
        var promisewater=  scene.addS3MTilesLayerByScp(that.s3murl[6], {
        name: "water"
          });
        Cesium.when.all([promisecqu,promisewater], (layers) => {
          var cqulayer=layers[0];
          var waterlayer=layers[1];

          cqulayer.selectEnabled = false;
          cqulayer.visible = false;
          waterlayer.selectEnabled = false;
          waterlayer.visible = false;
          //cqulayer.visibleDistanceMax =800
          //return;
          that.addLeftClick();
          scene.camera.changed.addEventListener(function() {
            //console.log(that.viewer.camera.positionCartographic.height);
            var height = that.viewer.camera.positionCartographic.height;
            // if (height > 1800) {

            // }
            if (height > 2600) {
              //切换到概况页面
              // if (that.$parent.$parent.$parent.$refs.content.changePage) {
              //   that.$parent.$parent.$parent.$refs.content.changePage(0);
              // }
            } else if (height > 2000) {
              layer.brightness = 1;
            } else if (2000 > height && height > 1900) {
              layer.brightness = 0.9;
            } else if (1900 > height && height > 1850) {
              layer.brightness = 0.8;
            } else if (1850 > height && height > 1800) {
              layer.brightness = 0.75;
            } else if (1800 > height && height > 1700) {
              layer.brightness = 0.7;
            } else if (1700 > height && height > 1600) {
              layer.brightness = 0.6;
            } else if (1600 > height && height > 1500) {
              layer.brightness = 0.5;
            } else if (1500 > height && height > 1400) {
              layer.brightness = 0.4;
            } else if (1400 > height && height > 1200) {
              layer.brightness = 0.2;
            }
            if (that.viewer.camera.positionCartographic.height < 1000) {
              //if (that.$parent.$parent.$parent.$refs.content.changePage)
              //that.$parent.$parent.$parent.$refs.content.changePage(2);

              layer.visible = false;
              cqulayer.visible = true;
              waterlayer.visible = true;
              window.clearInterval(that.tanzhaodengtimer);
              if (that.tanzhaodeng) {
                that.tanzhaodeng.setDistDirByPoint([
                  104.07951378542454,
                  30.542005148499499,
                  599
                ]);
                that.tanzhaodeng=null
              }
              var road = that.viewer.dataSources._dataSources.firstOrDefault(
                t => t.name == "road"
              );
              if (road) {
                road.show = false;
              }
            } else {
              layer.visible = true;
              cqulayer.visible = false;
              waterlayer.visible = false;
              var road = that.viewer.dataSources._dataSources.firstOrDefault(
                t => t.name == "road"
              );
              if (road) {
                road.show = true;
              }
            }
          });
        });
      });
    },

    goHome() {
      var _this = this;
      this.viewer.scene.camera.flyTo({
        destination: new Cesium.Cartesian3.fromElements(
          -1336235.5589402015,
          5332351.912093125,
          3239839.236391837
        ),
        // destination:new Cesium.Cartesian3.fromDegrees(104.067090937120720, 30.542358378696374, 2000),
        orientation: {
          heading: 0.032452387143920625,
          pitch: -0.729098052421163,
          roll: 4.923119689692612e-10
        },
        duration: 3,
        complete: function() {
          var viewer = _this.viewer;
          var time = 2000;
          var pos = new Cesium.Cartesian3.fromDegrees(
            104.06851729963796,
            30.541552215608974,
            10
          );
          var datas = _this.viewer.dataSources._dataSources.firstOrDefault(
            t => t.name == "road"
          );
          if (datas) return;
          var promiseroute11 = Cesium.loadJson("/static/datas/moreroad.json");
          promiseroute11.then(datasource => {
            var roaddatasource = new Cesium.CustomDataSource("road");
            viewer.dataSources.add(roaddatasource);
            var colors = [];
            for (let i = 1; i < 6; i++) {
              var color = "";
              switch (i) {
                case 1:
                  color = Cesium.Color.AQUA;
                  break;
                case 2:
                  color = Cesium.Color.AQUAMARINE;
                  break;
                case 3:
                  color = Cesium.Color.GREENYELLOW;
                  break;
                case 4:
                  color = Cesium.Color.CYAN;
                  break;
                case 5:
                  color = Cesium.Color.CHARTREUSE;
                  break;
                default:
                  break;
              }
              colors.push({
                level: i,
                color: color
                // color:Cesium.Color.fromCssColorString('rgb('+ Math.random() * 100+','+ Math.random() * 100+','+ Math.random() * 100+')').withAlpha(i/10)
              });
            }
            for (let index = 0; index < datasource.features.length; index++) {
              const element = datasource.features[index];
              var positions = [];
              var positions1 = [];
              if (index == 0) _this.linetest = element.geometry.paths[0];
              for (let j = 0; j < element.geometry.paths[0].length; j++) {
                const coordinates = element.geometry.paths[0][j];

                const coord = coordinates;
                //positions.push(new Cesium.Cartesian3.fromDegrees(coord[0],coord[1],5))
                positions.push(coord[0], coord[1], 10);
                positions1.push(coord[0], coord[1], 7);
              }
              var entity = new Cesium.Entity({
                polyline: new Cesium.PolylineGraphics({
                  positions: Cesium.Cartesian3.fromDegreesArrayHeights(
                    positions1
                  ),
                  material: new Cesium.PolylineTrailMaterialProperty({
                    //设置Glow材质
                    //glowPower: 0.3,
                    color: Cesium.Color.ORANGERED,
                    //outlineWidth : 0,
                    //outlineColor : Cesium.Color.BLACK
                    trailLength: 0.2,
                    period: 5.0
                  }),
                  width: parseFloat(element.attributes.level) * 2,
                  hMax: 500000
                  //distanceDisplayCondition:new Cesium.DistanceDisplayCondition(1600,5000)
                })
              });
              var entity1 = new Cesium.Entity({
                polyline: new Cesium.PolylineGraphics({
                  positions: Cesium.Cartesian3.fromDegreesArrayHeights(
                    positions
                  ),
                  material: new Cesium.PolylineGlowMaterialProperty({
                    //设置Glow材质
                    glowPower: 0.1,
                    color: colors
                      .firstOrDefault(
                        t => t.level == parseInt(element.attributes.level)
                      )
                      .color.withAlpha(
                        parseFloat(element.attributes.level) / 10
                      )
                  }),
                  width: parseFloat(element.attributes.level) * 8
                  // distanceDisplayCondition:new Cesium.DistanceDisplayCondition(1600,5000)
                })
              });
              //_this.viewer.entities.add(entity);
              //_this.viewer.entities.add(entity1);
              roaddatasource.entities.add(entity);
              roaddatasource.entities.add(entity1);
            }
            //_this.loadBaoanRout();
            //_this.doCirCleFly();
            //window.setTimeout(_this.CirclrFly,800)
            // _this.loadShaoMiaoxian(
            //   "/static/img/scanline.png",
            //   "/static/datas/ScanLine.json",
            //   50
            // );
            _this.tanZhaodeng();
          });
        }
      });
    },
    CirclrFly() {
      var pos = new Cesium.Cartesian3.fromDegrees(
        104.06851729963796,
        30.541552215608974,
        10
      );
      this.viewer.camera.flyCircle(pos);
      this.viewer.camera.flyCircleLoop = false;
    },
    tanZhaodeng() {
      this.loadtanzhaodeng(
        "/static/img/1.png",
        "/static/datas/lightpoint.json",
        50
      );
    },
    loadquanjing(){
      this.clearDataSourcees();
      var iconsource = new Cesium.CustomDataSource("quanjing");
        this.viewer.dataSources.add(iconsource);
        var entity=new Cesium.Entity({
          position:Cesium.Cartesian3.fromDegrees(104.06851729963796,
        30.541552215608974,
        100),
          billboard:new Cesium.BillboardGraphics({
            image:"/static/img/qj.png",
            scale:0.6,
            distanceDisplayCondition:new Cesium.DistanceDisplayCondition(1000,5000)
          })
        })
        entity.properties=new Cesium.PropertyBag({
            url: this.qjurl
          })
        iconsource.entities.add(entity);
    },
    loadAnfangVideo() {
      var that = this;
      var promiseroute11 = Cesium.loadJson("/static/datas/qu.json");
      promiseroute11.then(datasource => {
        var iconsource = new Cesium.CustomDataSource("anfangvideo");
        that.viewer.dataSources.add(iconsource);
        for (let index = 0; index < datasource.features.length; index++) {
          const feature = datasource.features[index];
          //var pos = Cesium.Cartesian3.fromDegrees(feature.geometry.x, feature.geometry.y, 30);
          var entity = new Cesium.Entity({
            position: that.computebillboradheight(
              feature.geometry.x,
              feature.geometry.y,
              150,
              index % 2 == 0 ? 1 : -1
            ),
            billboard: {
              scale: 0.75,
              verticalOrigin: Cesium.VerticalOrigin.BOTTOM,
              image: "/static/img/摄像头.png",
              distanceDisplayCondition: new Cesium.DistanceDisplayCondition(
                1000,
                7000
              )
              //image: '/static/img/B区.png'
            }
          });
          //this.computeCirclularFlight(feature.geometry.x, feature.geometry.y, 0.000003)
          entity.properties = new Cesium.PropertyBag({
            anfang: feature.attributes.区名
          });
          iconsource.entities.add(entity);
        }
      });
    },
    loadBaoanRout() {
      var that = this;
      this.viewer.clock.currentTime=this.start.clone();
      this.viewer.clock.multiplier = 0.5;
      var promiseroute11 = Cesium.loadJson("/static/datas/baoanrout.json");
      promiseroute11.then(datasource => {
        var iconsource = new Cesium.CustomDataSource("BaoanRout");
        that.viewer.dataSources.add(iconsource);

        var pos = [];
        var property = new Cesium.SampledPositionProperty();
        for (let index = 0; index < datasource.features.length; index++) {
          const feature = datasource.features[index];
          pos.push(feature.geometry.x, feature.geometry.y,  10)

          var time = Cesium.JulianDate.addSeconds(
            that.start,
            (datasource.features.length / 360)*index,
            new Cesium.JulianDate()
          );
          var endpos = Cesium.Cartesian3.fromDegrees(feature.geometry.x, feature.geometry.y,  10);
          property.addSample(time,endpos);
        }
        var entity=new Cesium.Entity({
          position:property,
          billboard:new Cesium.BillboardGraphics({
            image:'/static/img/保安.png',
             scale: 0.3,
          })
        })
        iconsource.entities.add(entity);
        iconsource.entities.add(new Cesium.Entity({
          polyline:new Cesium.PolylineGraphics({
            positions:Cesium.Cartesian3.fromDegreesArrayHeights(pos),
            width:2,
            material:Cesium.Color.AQUAMARINE
          })
        }));
        //that.viewer.entities.add(entity);
        that.viewer.trackedEntity=entity;
        that.viewer.flyTo(iconsource,{
            duration: 1.5
          });
      });
    },
    //加载批次的区域面
    loadPiciMian() {
      var _ = this;
      var promise = Cesium.GeoJsonDataSource.load(
        "/static/datas/picimian.json"
      );
      promise.then(dataSource => {
        dataSource.name = "picimian";
        _.viewer.dataSources.add(dataSource);
        var entities = dataSource.entities.values;
        var piciyaunqu = [];
        for (let index = 0; index < entities.length; index++) {
          const entity = entities[index];
          var pici = entity.properties.Type;
          var quname = entity.properties.Name;

          var color = Cesium.Color.AQUAMARINE.withAlpha(0.4);
          switch (pici._value) {
            case "第一批次":
              color = Cesium.Color.fromCssColorString("#5676ff");
              break;
            case "第二批次":
              color = Cesium.Color.fromCssColorString("#00dca0");
              break;
            case "第三批次":
              color = Cesium.Color.fromCssColorString("#0084ff");
              break;
            case "第四批次":
              color = Cesium.Color.fromCssColorString("#FFF");
              break;
            default:
              //color=Cesium.Color.fromCssColorString("#00dca0").withAlpha(0.5)
              break;
          }
          piciyaunqu.push({
            pici: pici._value,
            name: quname._value,
            color: color
          });
          entity.polygon.material = color.withAlpha(0.5);
          entity.polygon.outline = false;
          entity.polygon.extrudedHeight = 70;
          entity.polygon.distanceDisplayCondition = new Cesium.DistanceDisplayCondition(
            1000,
            7000
          );
        }
        //return;
        Cesium.loadJson("/static/datas/qu.json").then(datas => {
          for (let index = 0; index < datas.features.length; index++) {
            const feature = datas.features[index];
            var pici = piciyaunqu.firstOrDefault(
              t => t.name.indexOf(feature.attributes.区名) > -1
            );
            var point = new Cesium.Entity({
              position: Cesium.Cartesian3.fromDegrees(
                feature.geometry.x,
                feature.geometry.y,
                120
              ),
              // billboard:new Cesium.BillboardGraphics({
              //   image:"/static/img/第二批次.png",
              //   rotation:-60,
              // })
              label: new Cesium.LabelGraphics({
                text: pici.pici,
                //text:"第\n一\n批\n次",
                font: " 22px 平方 blod",
                fillColor: pici.color,
                backgroundColor: Cesium.Color.BLACK.withAlpha(0.2),
                showBackground: true,
                backgroundPadding: new Cesium.Cartesian2(2, 2),
                outlineColor: Cesium.Color.RED,
                outlineWidth: 1.5,
                distanceDisplayCondition: new Cesium.DistanceDisplayCondition(
                  1000,
                  7000
                )
              })
            });
            dataSource.entities.add(point);
          }
        });
      });
    },
    //加载产业配套
    loadChanYePeiTaoIcon() {
      var that = this;
      this.loadGeoJSONData(
        "/static/datas/chanyepeitao.json",
        "chanyepeitao",
        datasource => {
          var entities = datasource.entities.values;
          for (let index = 0; index < entities.length; index++) {
            const entity = entities[index];
            var ellipsoid = that.viewer.scene.globe.ellipsoid;
            var cartographic = ellipsoid.cartesianToCartographic(
              new Cesium.Cartesian3(
                entity.position._value.x,
                entity.position._value.y,
                entity.position._value.z
              )
            );
            var lat = Cesium.Math.toDegrees(cartographic.latitude);
            var lon = Cesium.Math.toDegrees(cartographic.longitude);
            var alt = cartographic.height;
            entity.position = Cesium.Cartesian3.fromDegrees(lon, lat, alt + 30);
            entity.billboard = new Cesium.BillboardGraphics({
              image:
                "/static/img/chanyepeitao/" + entity.properties._type + ".png",
              scale: 0.5,
              verticalOrigin: Cesium.VerticalOrigin.BOTTOM,
              distanceDisplayCondition: new Cesium.DistanceDisplayCondition(
                0,
                10000
              )
            });
          }
          that.viewer.dataSources.add(datasource);
        }
      );
    },
    //加载重点企业
    loadImportantQiYe() {
      //凡客超市
      //"OBJECTID": 11,
      var that = this;
      this.loadGeoJSONData(
        "/static/datas/chanyepeitao.json",
        "chanyepeitao",
        datasource => {
          var entities = datasource.entities.values;
          for (let index = 0; index < entities.length; index++) {
            const entity = entities[index];
            if (entity.properties.OBJECTID.getValue() != 11) {
              entity.show = false;
            }
            var ellipsoid = that.viewer.scene.globe.ellipsoid;
            var cartographic = ellipsoid.cartesianToCartographic(
              new Cesium.Cartesian3(
                entity.position._value.x,
                entity.position._value.y,
                entity.position._value.z
              )
            );
            var lat = Cesium.Math.toDegrees(cartographic.latitude);
            var lon = Cesium.Math.toDegrees(cartographic.longitude);
            var alt = cartographic.height;
            entity.position = Cesium.Cartesian3.fromDegrees(lon, lat, alt + 50);
            entity.billboard = new Cesium.BillboardGraphics({
              image: "/static/img/腾讯1.png",
              scale: 0.5,
              verticalOrigin: Cesium.VerticalOrigin.BOTTOM,
              distanceDisplayCondition: new Cesium.DistanceDisplayCondition(
                0,
                10000
              )
            });
          }
          that.viewer.dataSources.add(datasource);
        }
      );
    },
    //加载geojson数据
    loadGeoJSONData(url, datasourcesname, callback) {
      var _ = this;
      var promise = Cesium.GeoJsonDataSource.load(url);
      promise.then(datasource => {
        datasource.name = datasourcesname;
        if (callback) {
          callback(datasource);
        } else {
          _.viewer.dataSources.add(datasource);
        }
      });
    },
    loadEsriJSONData(url, datasourcesname, callback) {
      var _ = this;
      Cesium.loadJson("/static/datas/qu.json").then(source => {
        var datasource = new Cesium.CustomDataSource(datasourcesname);
        _.viewer.dataSources.add(datasource);
        if (callback && typeof callback == "function") {
          callback(datasource, source);
        }
      });
    },
    //添加点击批次面
    addLeftClick() {
      var _this = this;
      this.reAddScreenSpaceEvent(movement => {
        var pickedFeature = _this.viewer.scene.pick(movement.position);
        _this.showVideo = false;
        _this.showwin = false;
        _this.showFace = false;
         _this.showIframe=false; 
        var offset = 10;
        if (!Cesium.defined(pickedFeature)) return;
        var entity = pickedFeature.id;
        if (!entity.properties) return;
        if (entity.properties.hasProperty("Type")) {
          var pici = entity.properties.Type.getValue();
          if (!Cesium.defined(pici)) return;
          //todo 左右切换到批次的数据
          if (_this.$parent.$parent.$parent.$refs.content.changePage)
            _this.$parent.$parent.$parent.$refs.content.changePage(1);
          _this.viewer.flyTo(pickedFeature.id, {
            duration: 1.5
          });
        } else if (entity.properties.hasProperty("yuanqu")) {
          var yuanqu = entity.properties.yuanqu.getValue();
          if (!Cesium.defined(yuanqu)) return;
          //todo 左右切换到园区的数据
          _this.$parent.$parent.$parent.$refs.content.changePage(2);
          //定位到园区
          _this.viewer.flyTo(pickedFeature.id, {
            duration: 1.5
          });
          _this.loadImportantQiYe();
        } else if (entity.properties.hasProperty("alarm")) {
          var alarmtype = entity.properties.alarm.getValue();
          if (alarmtype == "identifyface") {
            //todo 展示摄像头
            var htmlOverlay = document.getElementById("warnFace");
            htmlOverlay.style.top = movement.position.y + offset + "px";

            htmlOverlay.style.left = movement.position.x + offset + "px";
            _this.showFace = true;
          } else if (alarmtype == "smartSafty") {
            //todo展示安防信息
            var htmlOverlay = document.getElementById("warnWin");
            htmlOverlay.style.top = movement.position.y + offset + "px";

            htmlOverlay.style.left = movement.position.x + offset + "px";
            _this.showwin = true;
          }
        } else if (entity.properties.hasProperty("daoza")) {
          var datasource = _this.viewer.dataSources._dataSources.firstOrDefault(
            t => t.name == "daoza"
          );
          if (datasource) {
            for (
              let index = 0;
              index < datasource.entities.values.length;
              index++
            ) {
              const en = datasource.entities.values[index];
              en.billboard.image = "/static/img/icons/daoza.png";
            }
          }
          var daoza = entity.properties.daoza.getValue();
          entity.billboard.image = "/static/img/icons/daozaselected.png";
          var htmlOverlay = document.getElementById("overlayer");
          htmlOverlay.style.top = movement.position.y + offset + "px";

          htmlOverlay.style.left = movement.position.x + offset + "px";
          _this.showVideo = true;
        } else if (entity.properties.hasProperty("ty")) {
          if (_this.$parent.$parent.$parent.$refs.content.changePage)
            _this.$parent.$parent.$parent.$refs.content.changePage(4);
        } else if (entity.properties.hasProperty("anfang")) {
          var htmlOverlay = document.getElementById("overlayer");
          htmlOverlay.style.top = movement.position.y + offset + "px";

          htmlOverlay.style.left = movement.position.x + offset + "px";
          _this.showVideo = true;
        }
        else if(entity.properties.hasProperty("url")){
          //展示全景      
           var url = entity.properties.url.getValue();
          _this.showIframe=true; 
           //window.open(url);  
        }
      }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
    },
    //添加鼠标mouseover事件
    addMouseOver() {
      var _this = this;
      this.reAddScreenSpaceEvent(movment => {
        var pickedFeature = _this.viewer.scene.pick(movement.position);
        _this.showVideo = false;
        _this.showwin = false;
        _this.showFace = false;
        if (!Cesium.defined(pickedFeature)) return;
        var entity = pickedFeature.id;
        if (entity.properties.hasProperty("daoza")) {
          //todo 显示出入情况
        }
      }, Cesium.ScreenSpaceEventType.MOUSE_MOVE);
    },
    //移除屏幕事件
    removeScreenSpaceEvent(eventType) {
      this.viewer.screenSpaceEventHandler.removeInputAction(eventType);
    },
    //重新绑定事件
    reAddScreenSpaceEvent(action, type) {
      this.removeScreenSpaceEvent(type);
      this.viewer.screenSpaceEventHandler.setInputAction(action, type);
    },
    //添加探照灯，定点投射改变显示位置
    loadtanzhaodeng(image, jsonurl, step) {
      var viewer = this.viewer;
      viewer.scene.sun.show = false;
      viewer.scene.moon.show = false;
      viewer.scene.skyBox.show = false;
      viewer.scene.skyAtmosphere.show = false;
      viewer.scene.bloomEffect.threshold = 0.6;
      viewer.scene.bloomEffect.bloomIntensity = 0.4;

      this.tanzhaodeng = new Cesium.ProjectionImage(this.viewer.scene);
      this.tanzhaodeng.viewPosition = [
        104.07951378542454,
        30.542005148499499,
        600
      ];
      this.tanzhaodeng.setImage({
        url: image
      });
      this.tanzhaodeng.build();
      this.tanzhaodeng.hintLineVisible = false;

      //projectionImage.setDistDirByPoint([104.06411378542454, 30.542005148499499, 14.916258936093001]);
      // projectionImage.distance = 1050;
      // projectionImage.horizontalFov = 90;
      // projectionImage.verticalFov = 90;
      // var projectionImage1 = new Cesium.ProjectionImage(this.viewer.scene);
      // projectionImage1.viewPosition = [104.06851729963796,30.541552215608974, 288.1624044906759];
      // projectionImage1.setImage({
      //     url: '/static/img/1.png'
      // });
      // projectionImage1.build();
      // projectionImage1.hintLineVisible = false;
      // projectionImage1.setDistDirByPoint([104.06851729963796,30.541552215608974, 14.916258936093001]);
      // projectionImage1.distance = 500;
      // projectionImage1.horizontalFov = 90;
      // projectionImage1.verticalFov = 60;
      //projectionImage.pitch=90;
      var that = this;
      var promiseroute11 = Cesium.loadJson(jsonurl);
      promiseroute11.then(datasource => {
        var flag = 0;
        var a = 1;
        // var mid=parseInt(datasource.features.length/2);
        //    that.tanzhaodeng.viewPosition = [
        //     datasource.features[mid].geometry.x,
        //     datasource.features[mid].geometry.y,
        //     1000
        //   ];
        that.tanzhaodengtimer = window.setInterval(() => {
          //that.tanzhaodeng.viewPosition = [datasource.features[flag].geometry.x, datasource.features[flag].geometry.y, 1000]
          that.tanzhaodeng.setDistDirByPoint([
            datasource.features[flag].geometry.x,
            datasource.features[flag].geometry.y,
            10
          ]);
          flag += 10 * a;
          if (flag >= datasource.features.length) {
            a = -1;
            flag += 10 * a;
          }
          if (flag <= 0) {
            a = 1;
            flag += 10 * a;
          }
          return;
          if (that.tanzhaodeng.pitch >= 90 || that.tanzhaodeng.pitch <= -90) {
            flag *= -1;
          }
          that.tanzhaodeng.pitch += 10 * flag;
          //projectionImage.direction+=10*flag;
        }, step);
      });
    },
    loadShaoMiaoxian(image, jsonurl, step) {
      if (!step) step = 30;
      var scanline = new Cesium.ProjectionImage(this.viewer.scene);
      scanline.viewPosition = [104.04195645889286, 30.528361263476683, 1000];
      scanline.setImage({
        url: image
      });
      scanline.direction = 200;
      scanline.horizontalFov = 170;
      scanline.verticalFov = 5;
      //projectionImage.pitch=90;
      scanline.setDistDirByPoint([
        104.04195645889286,
        30.528361263476683,
        -100
      ]);
      scanline.build();
      scanline.hintLineVisible = false;
      var that = this;
      //return;
      var promiseroute11 = Cesium.loadJson(jsonurl);
      promiseroute11.then(datasource => {
        var flag = 0;
        var a = 1;
        var mid = parseInt(datasource.features.length / 2);
        scanline.viewPosition = [
          datasource.features[mid].geometry.x,
          datasource.features[mid].geometry.y,
          10000
        ];
        that.viewer.scene.postRender.addEventListener(function(time) {
          scanline.setDistDirByPoint([
            datasource.features[flag].geometry.x,
            datasource.features[flag].geometry.y,
            -500
          ]);
          flag += step * a;
          if (flag >= datasource.features.length) {
            //   window.clearInterval(timer);
            //   scanline=null;
            //that.viewer.scene.postRender.removeEventListener(this);
            a = -1;
            flag += step * a;
          }
          if (flag <= 0) {
            a = 1;
            flag += step * a;
          }
        });

        return;
        var timer = window.setInterval(() => {
          scanline.setDistDirByPoint([
            datasource.features[flag].geometry.x,
            datasource.features[flag].geometry.y,
            -100
          ]);
          flag += 50 * a;
          if (flag >= datasource.features.length) {
            //   window.clearInterval(timer);
            //   scanline=null;
            a = -1;
            flag += 50 * a;
          }
          if (flag <= 0) {
            a = 1;
            flag += 50 * a;
          }
        }, 100);
      });
    },
    //添加报警的
    addCircleRaise(pos, height, min, max, datasource) {
      for (let index = 0; index < 4; index++) {
        var Axis = this.computeaxis(
          min + ((max - min) / 4) * index,
          10 * index
        );

        var entity = new Cesium.Entity({
          position: pos,
          ellipse: {
            semiMinorAxis: Axis,
            semiMajorAxis: Axis,
            height: height,
            material: Cesium.Color.RED.withAlpha(0.5 - index / 10),
            outline: false,
            fill: true,
            distanceDisplayCondition: new Cesium.DistanceDisplayCondition(
              1000,
              10000
            )
          }
        });
        var entity1 = new Cesium.Entity({
          position: pos,
          ellipsoid: {
            radii: new Cesium.Cartesian3(
              (min / 4) * index,
              (min / 4) * index,
              (min / 4) * index
            ),
            material: Cesium.Color.RED.withAlpha(0.5 - index / 10),
            distanceDisplayCondition: new Cesium.DistanceDisplayCondition(
              1000,
              10000
            )
          }
        });
        if (datasource) {
          datasource.entities.add(entity);
          datasource.entities.add(entity1);
        } else {
          this.viewer.entities.add(entity);
          this.viewer.entities.add(entity1);
        }
      }
    },
    computeaxis(baselength, start) {
      var property = new Cesium.SampledProperty(Number);
      for (var i = 0; i <= 360 - start; i += 1) {
        var time = Cesium.JulianDate.addSeconds(
          this.start,
          i,
          new Cesium.JulianDate()
        );
        //var position = baselength+ Math.abs(Math.sin(i*Math.PI/180))*20
        var position = baselength + (i % (10 + start));
        property.addSample(time, position);
      }
      return property;
    },

    clearDataSourcees(datasourcesnames) {
      // if (!datasourcesnames||datasourcesnames.length == 0) {
      //   this.viewer.dataSources.removeAll();
      // } else {

      // }
      if (!datasourcesnames) datasourcesnames = [];
      var deletesource = this.viewer.dataSources._dataSources.where(
        t => datasourcesnames.indexOf(t.name) == -1
      );
      deletesource.forEach(data => {
        if (data.name != "road") this.viewer.dataSources.remove(data);
      });
      deletesource = null;
      this.showVideo = false;
      this.showwin = false;
      this.showFace = false;
      this.showIframe=false; 
    },
    //加载报警的点位
    loadAlarmCircle() {
      this.clearDataSourcees();
      var _ = this;
      var promiseroute11 = Cesium.loadJson("/static/datas/qu.json");
      promiseroute11.then(datasource => {
        var iconsource = new Cesium.CustomDataSource("alarm");
        _.viewer.dataSources.add(iconsource);
        for (let index = 0; index < datasource.features.length; index++) {
          if (index > 0) continue;
          const feature = datasource.features[index];
          var pos = Cesium.Cartesian3.fromDegrees(
            feature.geometry.x,
            feature.geometry.y,
            30
          );
          _.addCircleRaise(pos, 30, 50, 100, iconsource);
          iconsource.entities.add({
            polyline: {
              positions: [
                pos,
                Cesium.Cartesian3.fromDegrees(
                  feature.geometry.x,
                  feature.geometry.y,
                  85
                )
              ],
              width: 2,
              material: Cesium.Color.RED.withAlpha(0.7),
              followSurface: false
            }
          });
          iconsource.entities.add({
            position: Cesium.Cartesian3.fromDegrees(
              feature.geometry.x,
              feature.geometry.y,
              85
            ),
            ellipsoid: {
              radii: new Cesium.Cartesian3(2, 2, 2),
              material: Cesium.Color.RED.withAlpha(0.7),
              distanceDisplayCondition: new Cesium.DistanceDisplayCondition(
                0,
                10000
              )
            }
          });

          var alarmicon = new Cesium.Entity({
            billboard: {
              scale: 0.6,
              verticalOrigin: Cesium.VerticalOrigin.BOTTOM,
              scaleByDistance: new Cesium.NearFarScalar(1.5e2, 0.6, 1.5e7, 0.1),
              image:
                "/static/img/icons/" +
                (index % 2 == 0 ? "smartSafty.png" : "identifyface.png"),
              distanceDisplayCondition: new Cesium.DistanceDisplayCondition(
                0,
                10000
              )
            },
            position: _.computebillboradheight(
              feature.geometry.x,
              feature.geometry.y,
              150,
              index % 2 == 0 ? 1 : -1
            )
            // Cesium.Cartesian3.fromDegrees(
            //     feature.geometry.x,
            //     feature.geometry.y,
            //     100
            //   )
          });
          alarmicon.properties = new Cesium.PropertyBag({
            alarm: index % 2 == 0 ? "smartSafty" : "identifyface"
          });
          iconsource.entities.add(alarmicon);
          _.viewer.flyTo(iconsource, {
            duration: 1.5
          });
        }
      });
    },
    loadchanye() {
      this.clearDataSourcees();
      this.loadPiciMian();
      this.loadQuicon();
      this.goHome();
    },
    //核心系统
    loadAlarmSysCircle() {
      //this.goHome()
      this.clearDataSourcees();
      var _ = this;
      var promiseroute11 = Cesium.loadJson("/static/datas/qu.json");
      promiseroute11.then(datasource => {
        var iconsource = new Cesium.CustomDataSource("alarmsys");
        _.viewer.dataSources.add(iconsource);
        for (let index = 0; index < datasource.features.length; index++) {
          if (index != 1) continue;
          const feature = datasource.features[index];
