    },
    //加载分区的图标
    loadQuicon() {
      var that = this;

      var promiseroute11 = Cesium.loadJson("/static/datas/qu.json");
      promiseroute11.then(datasource => {
        var iconsource = new Cesium.CustomDataSource("quicon");
        that.viewer.dataSources.add(iconsource);
        for (let index = 0; index < datasource.features.length; index++) {
          const feature = datasource.features[index];
          //var pos = Cesium.Cartesian3.fromDegrees(feature.geometry.x, feature.geometry.y, 30);
          var entity = new Cesium.Entity({
            position: that.computebillboradheight(
              feature.geometry.x,
              feature.geometry.y,
              150,
              index % 2 == 0 ? 1 : -1
            ),
            billboard: {
              scale: 0.75,
              verticalOrigin: Cesium.VerticalOrigin.BOTTOM,
              image: "/static/img/" + feature.attributes.区名 + ".png",
              distanceDisplayCondition: new Cesium.DistanceDisplayCondition(
                1000,
                7000
              )
              //image: '/static/img/B区.png'
            }
          });
          //this.computeCirclularFlight(feature.geometry.x, feature.geometry.y, 0.000003)
          entity.properties = new Cesium.PropertyBag({
            yuanqu: feature.attributes.区名
          });
          iconsource.entities.add(entity);
        }
      });
    },
    computeCirclularFlight(lon, lat, radius) {
      var polylines = [];
      for (var i = 45; i <= 360; i += 45) {
        var radians = Cesium.Math.toRadians(i);
        lon = lon + radius * 1.5 * Math.cos(radians);
        lat = lat + radius * Math.sin(radians);
        //var position = Cesium.Cartesian3.fromDegrees(lon,lat , 10);
        var surfacePosition = Cesium.Cartesian3.fromDegrees(lon, lat, 10);
        var heightPosition = Cesium.Cartesian3.fromDegrees(lon, lat, 150);

        //WebGL Globe only contains lines, so that's the only graphics we create.
        var polyline = new Cesium.PolylineGraphics();
        // polyline.material = new Cesium.PolylineTrailMaterialProperty({ //设置Glow材质
        //     //glowPower: 0.3,
        //     color:  Cesium.Color.GREEN,
        //     trailLength: 0.7,
        //     period: 2.0
        // });
        //polyline.material = new Cesium.ColorMaterialProperty(Cesium.Color.GREEN)
        polyline.material = new Cesium.CompositeMaterialProperty(
          Cesium.Color.GREEN
        );
        polyline.width = new Cesium.ConstantProperty(2);
        polyline.followSurface = new Cesium.ConstantProperty(false);
        polyline.positions = new Cesium.ConstantProperty([
          surfacePosition,
          heightPosition
        ]);

        polyline.show = true;

        var lineentity = new Cesium.Entity({
          polyline: polyline
        });
        //this.viewer.entities.add(lineentity);
        polylines.push(lineentity);
      }
      return;
      var that = this;
      var start = Cesium.JulianDate.now();
      var clock = new Cesium.Clock({
        startTime: start,
        currentTime: start,
        //stopTime: stop,
        clockRange: Cesium.ClockRange.UNBOUNDED,
        clockStep: Cesium.ClockStep.SYSTEM_CLOCK_MULTIPLIER,
        multiplier: 1000
      });

      function tick() {
        //var lineentities = that.getRandomArrayElements(polylines, 4);
        polylines.forEach(function(polylin, index) {
          //polylin.polyline.show = lineentities.indexOf(polylin) > -1;
          polylin.polyline.show = true;
          polylin.polyline.material = new Cesium.ColorMaterialProperty(
            Cesium.Color.GREEN.withAlpha(that.randomNum(0, 9) / 10)
          );
          // var flag = 0;
          // window.setInterval(function () {
          //     flag++;
          //     lineentities.forEach((item, i) => {
          //         item.polyline.material = new Cesium.ColorMaterialProperty(Cesium.Color.GREEN.withAlpha(1 - (flag / 10)))
          //     })
          //     if (flag >= 10) flag = 0;
          // }, 50)
        });
        clock.tick();
        //Cesium.requestAnimationFrame(tick)
      }
      // Cesium.requestAnimationFrame(tick)
      clock.onTick.addEventListener(tick);
      clock.tick();
    },
    getRandomArrayElements(arr, count) {
      var shuffled = arr.slice(0),
        i = arr.length,
        min = i - count,
        temp,
        index;
      while (i-- > min) {
        index = Math.floor((i + 1) * Math.random());
        temp = shuffled[index];
        shuffled[index] = shuffled[i];
        shuffled[i] = temp;
      }
      return shuffled.slice(min);
    },
    computebillboradheight(x, y, height, flag) {
      var property = new Cesium.SampledPositionProperty();
      var indexsec = 0;
      for (var i = 0; i <= 360; i += 15) {
        indexsec++;
        var time = Cesium.JulianDate.addSeconds(
          this.start,
          i,
          new Cesium.JulianDate()
        );
        var position = height + Math.sin((i * Math.PI) / 180) * 30 * flag;
        //var startpos=Cesium.Cartesian3.fromDegrees(x, y, 0);
        var endpos = Cesium.Cartesian3.fromDegrees(x, y, position);
        property.addSample(time, endpos);
      }

      return property;
    },
    doCirCleFly() {
      var flydatasource = new Cesium.CustomDataSource("flyManager");
      this.viewer.dataSources.add(flydatasource);
      // var entities=new Cesium.EntityCollection(flydatasource);
      var routes = new Cesium.RouteCollection(this.viewer.entities);
      //添加fpf飞行文件，fpf由SuperMap iDesktop生成
      var fpfUrl = "/static/datas/TFRJYSceneRoutes.fpf";
      routes.fromFile(fpfUrl);
      //初始化飞行管理
      var flyManager = new Cesium.FlyManager({
        scene: this.viewer.scene,
        routes: routes
      });
      flyManager.stopArrived.addEventListener(function(routeStop) {
        //routeStop.waitTime = 1; // 在每个站点处停留1s
      });
      flyManager.readyPromise.then(() => {
        flyManager.play();
      });
    },
    computeCirclularFlight(lon, lat, radius) {
      var property = new Cesium.SampledPositionProperty();
      for (var i = 0; i <= 360; i += 45) {
        var radians = Cesium.Math.toRadians(i);
        var time = Cesium.JulianDate.addSeconds(
          start,
          i,
          new Cesium.JulianDate()
        );
        var position = Cesium.Cartesian3.fromDegrees(
          lon + radius * 1.5 * Math.cos(radians),
          lat + radius * Math.sin(radians),
          Cesium.Math.nextRandomNumber() * 500 + 600
        );
        property.addSample(time, position);

        //Also create a point for each sample we generate.
        viewer.entities.add({
          position: position,
          point: {
            pixelSize: 8,
            color: Cesium.Color.TRANSPARENT,
            outlineColor: Cesium.Color.YELLOW,
            outlineWidth: 3
          }
        });
      }
      return property;
    },

    randomNum(minNum, maxNum) {
      switch (arguments.length) {
        case 1:
          return parseInt(Math.random() * minNum + 1, 10);
          break;
        case 2:
          return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
          break;
        default:
          return 0;
          break;
      }
    }
  }
};
</script>



