<template>
	<div class="company">
		<div class="buildMsg">

			<p><span>企业数量</span><label>{{comData.comNum}}</label><small>家</small></p>
			<p><span>员工数量</span><label>{{comData.employees}}</label><small>+</small></p>
			<p><span>办公面积</span><label>{{comData.area}}</label><small>㎡</small></p>
			<p><span>已租用面积</span><label>{{comData.areaUsed}}</label><small>㎡</small></p>
			<p><span>总产值</span><label>{{comData.total}}</label><small>万元</small></p>
			<p><span>税收总值</span><label>{{comData.tax}}</label><small>万元</small></p>
		</div>
		<div class="echartline" >
			<h3>企业营收</h3>
			<myLine ids="line" ref="line" :data="lineData"></myLine>
		</div>
	</div>
</template>

<script>
	import myLine from '@/components/module/echarts/line.vue'
	export default{
		components:{
			myLine
		},
		props:['comData'],
		data(){
			return{
				lineData:{
					data:[25,45,25,40,98,55,75],
					dataAxis:['2012','2013','2014','2015','2016','2017','2018'],
					unit:'(个)'
				}
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-left: 40px;
		width: 440px;
		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& h3,label{
			font-size: 24px;
		}
		& span{
			font-size: 20px;
		}
		& p{
			margin: 20px 0;
			border-bottom: 1px solid rgba(255,255,255,.2);
		}
		& .buildMsg{
			& span{
				width: 10em;
				display: inline-block;
			}
			& label{
				 background: linear-gradient(to top, #00c0ff, #0090ff);
		        -webkit-background-clip: text;
		        color: transparent;
		        font-size: 40px;
				width: 4em;
				display: inline-block;
			}
			& small{
				font-size: 24px;
				 background: linear-gradient(to top, #00c0ff, #0090ff);
		        -webkit-background-clip: text;
		        color: transparent;
		        width: 2em;
		        display: inline-block;
		        text-align: right;
			}
		}
	}
	.echartline{
		height: 250px;
		width: 480px;
	}
</style>


