<template>
	<div style="height:100%;" :id="ids">

	</div>
</template>

<script>
	export default{
		props:{
			ids:{
				type:String,
				default:"echartline"
			},
			data:{
				default:{
					title:"",
					series:[],
					dataAxis:[]
				}
			}
		},
		data(){
			return{
				
			}
		},
		methods:{
			setEcharts( series, dataAxis,unit) {
				let echarts=this.$echarts;
				var dataShadow = [];
				var legend=[];
				for(var item in series){
					legend.push(item.name)
				}
				var option = {
					title: {
						show:false,
					},
					color:['#02ff38','rgba(0,192,255,1)'],
					tooltip : {
				        trigger: 'axis',
				        axisPointer: {
				            type: 'cross',
				            label: {
				                backgroundColor: '#0084ff',
				                fontSize:20
				            }
				        }
				    },
				    legend:{
				    	top:10,
				    	right:0,
				    	itemWidth:15,
				    	itemheight:5,
				    	textStyle:{
				    		color:"#b3d9ff"
				    	}
				    },
				    xAxis: {
				        type: 'category',
				        data: dataAxis,
				         boundaryGap: false,
				         axisTick: {
							show: false
						},
						splitLine: {
							show:true,
							lineStyle:{
								color:'rgba(255,255,255,.05)'
							}
						},
						axisLine: {
							lineStyle:{
								color:'rgba(255,255,255,.05)'
							}
						},
						axisLabel:{
							color:'#fff',
							fontSize:16,
							interval:0
						}
				    },
				    yAxis: {
				        type: 'value',
				        name:unit,
				        nameTextStyle:{
				        	color:'#fff'
				        },
				        splitNumber:3,
				        nameGap:30,
				        min: function(value) {
						    return value.min - 20;
						},
						axisTick: {
							show: false
						},
						splitLine:{
							show:false
						},
						axisLine: {
							show: false
						},
						axisLabel:{
							color:'#fff',
							fontSize:18,
							align:'right',
							showMinLabel:false
						}
				    },
					grid: {
						left: 60,
						right:20,
						top: '60px',
						bottom: "50px",
						show: false
					},
				    series: series
				};

				let myChart=this.$echarts.init(document.getElementById(this.ids))
				myChart.setOption(option);

			}
		},
		watch:{
			
		},
		created(){
			
		},
		mounted(){
			this.setEcharts(this.data.series,this.data.dataAxis,this.data.unit);
		},
		destoryed(){
			
		}
	}
</script>

<style>
</style>


