<template>
	<div style="height:100%;" :id="ids">

	</div>
</template>

<script>
	import 'echarts-liquidfill/dist/echarts-liquidfill.js'
	export default{
		props:{
			ids:{
				type:String,
				default:"water"
			},
			data:{
				default:[]
			},
		},
		data(){
			return{
				
			}
		},
		methods:{
			setEchartsPie(id, data) {
				var radius,center,series=[];
				
				if(!data||data.length<1){
					return;
				}
				data.forEach((item,i)=>{
					radius=parseInt(100/data.length);
					center=[parseInt(radius)*i+parseInt(radius/2)+'%','50%'];
					series.push({
				        type: 'liquidFill',
				        data: [item],
				        radius: radius*1.5+'%',
				       	color:[{
				            type: 'linear',
				            x: 0,
				            y: 1,
				            x2: 0,
				            y2: 0,
				            colorStops: [{
				                offset: 0,
				                color: '#009cff' // 0% 处的颜色
				            }, {
				                offset: 1,
				                color: '#00fdca' // 100% 处的颜色
				            }],
				            globalCoord: false // 缺省为 false
				        }],
				        center: center,
				        // outline  外边
				        outline: {
				             show: false,
				            borderDistance: 1,
				            itemStyle: {
				                borderWidth: 5,
				                borderColor: '#13FDCE',
				            },
				        },
				        label: {
				            normal: {
				                // textStyle: {
				                    color: '#fff',
				                    insideColor: 'yellow',
				                    fontSize: 30
				                // }
				            }
				        },
				        backgroundStyle: {
				            color: '#272c30'
				        }
				    })
				})
				console.log(series)
				this.$nextTick(()=>{
					let option = {
					    series: series
				    }
					let myChart=this.$echarts.init(document.getElementById(id))
					myChart.setOption(option);
				})
				
				}

			},
		watch:{
			
		},
		created(){
			
		},
		mounted(){
			this.setEchartsPie(this.ids, this.data);
		},
		destoryed(){
			
		}
	}
</script>

<style>
</style>


