<template>
	<div class="company">
		<div class="comMsg">
			<div class="buildMsg">

				<p><span>企业注册总数</span><label>{{comData.comTotal}}</label><small>家</small></p>
				<p><span>昨日企业注册数</span><label>{{comData.regist}}</label><small>家</small></p>
				<p><span>服务总数</span><label>{{comData.serve}}</label><small>家</small></p>
			</div>
		</div>
		<div class="echartline">
			<h3>评审项目个数</h3>
			<myLine ids="line3" ref="line3" :data="lineData"></myLine>
		</div>
		<div class="echartline" >
			<h3>投诉类别</h3>
			<myBar ids="projNum" ref="projNum" :data="barDatas" :color="colorLists"></myBar>			
		</div>
		
	</div>
</template>

<script>

	import myBar from '@/components/module/echarts/bar.vue'
	import myLine from '@/components/module/echarts/line.vue'
	export default{
		components:{
			myBar,myLine
		},
		data(){
			return{
				barDatas:{
					title:"",
					data:[7,3,6],
					dataAxis:['服务投诉','消费抱怨投诉','建设性投诉'],
					unit:''
				},
				comData:{
					comTotal:8567,
					regist:356,
					serve:6451

				},
				lineData:{
					data:[40,98,55,75],
					dataAxis:['2015','2016','2017','2018'],
					unit:'(个)'
				},
				colorLists:['#0084ff',"#0084ff"],
				indexList:[{
					title:'单日用水量',
					icon:'icon-water',
					unit:'M³',
					data:60
				},{
					title:'单日用电量',
					icon:'icon-electricity',
					unit:'M³',
					data:600
				},{
					title:'单日用气量',
					icon:'icon-gas',
					unit:'NM³',
					data:600
				},{
					title:'消防报警',
					icon:'icon-fire',
					unit:'次',
					data:6
				},{
					title:'敏感人群',
					icon:'icon-sensitive',
					unit:'个',
					data:6
				},{
					title:'电梯运行情况',
					icon:'icon-elevator small',
					unit:'',
					data:'正常'
				}]
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-top: 170px;
		margin-right: 40px;
		float: right;
		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& h3,label{
			font-size: 24px;
		}
		& span{
			font-size: 20px;
		}
		& p{
			margin: 15px 0;
		}
	}
	.echartline{
		height: 250px;
		width: 480px;
	}
	.buildMsg{
		width: 480px;
		p{border-bottom: 2px solid rgba(255,255,255,.24);text-align: right;}
		& span{
			width: 10em;
			font-size: 22px;
			display: inline-block;
			text-align: left;
	        margin-top:10px;
			float: left;
		}
		& label{
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        font-size: 40px;
			width: 3em;
			display: inline-block;
			font-weight: bold;
			text-align: left;
			font-family: 'DINOT';
		}
		& small{
			font-size: 20px;
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        width: 3em;
	        display: inline-block;
	        text-align: right;
	        float: right;
	        margin-top:10px;
	        font-family: 'DINOT';

		}
	}
</style>


