<template>
	<div class="p">
		<div class="left-bg">
			<div class="safe-width">
				<img src="../assets/img/title/yy-wlsb.png"/>
				<div class="carData percent-box" v-for="(item,index) in carData">
					<p><label>{{item.name}}</label><span><big>{{item.value}}</big><small>{{item.unit}}</small></span></p>
					<div class="line" style="margin-top: 0;">
						<div class="line-act" :style="'max-width:100%;'"></div>
					</div>
				</div>
				<el-row :gutter="20" class="percent-box" style="height: 220px;">
					<fill-water ids="fillWater" ref="fillWater" :data="water"></fill-water>
					<el-row :gutter="0" class="percent-box" style="margin-left: -10px;margin-top: -30px;">
						<el-col :span="8"><p>总带宽使用率</p></el-col>
						<el-col :span="8"><p>CPU使用率&nbsp;</p></el-col>
						<el-col :span="8"><p>硬盘使用率&nbsp;&nbsp;&nbsp;</p></el-col>
					</el-row>
				</el-row>
				
				<img src="../assets/img/title/yy-dksyl.png"/>
				<div class="echartline" style="height: 250px;">
					<myBar ids="barType" ref="barType" :data="barDatas" :color="colorLists"></myBar>	
				</div>
				
			</div>
		</div>
		<div class="right-bg">
			<div class="safe-width" style="float: right;margin-right: 40px;">
				<img src="../assets/img/title/yy-IDC.png"/>
				<el-row :gutter="20" class="percent-box">
					<el-col :span="24">
						<div style="height: 200px;padding: 0;">
							<pieAll ids="power" :data='seriesData' unit="个"> </pieAll>
							<div class="msg">
								<h1>正常数</h1>
								<p><big>6980</big><small>个</small></p>
								<h1>异常数</h1>
								<p class="error"><big class="green">20</big><small>个</small></p>
							</div>
						</div>
						<div style="height: 200px;padding: 0;position: relative;">
							<pieAll ids="power2" :data='seriesData2' unit="个"> </pieAll>
							<div class="msg">
								<h1>正常数</h1>
								<p><big>660</big><small>个</small></p>
								<h1>异常数</h1>
								<p class="error"><big class="green">20</big><small>个</small></p>
							</div>
						</div>
					</el-col>
				</el-row>
				<img src="../assets/img/title/yy-safe.png"/>
				<div class="wltj">
					<label>今日网络攻击数量</label><span><big>0</big>次</span>
				</div>
				<div class="echartsline" style="height: 250px;">
					<myLine ids="line3" ref="line3" :data="lineData"></myLine>
				</div>
			</div>
			

		</div>
	</div>	
</template>

<script>
//	企业详请
	import myLine from '@/components/module/echarts/echartLine.vue'
	import lines from '@/components/module/echarts/line.vue'
	import percentPie from './module/echarts/percent'
	import pieAll from './module/echarts/percentAll'
	import fillWater from './module/echarts/fillWater'
	import myBar from './module/echarts/echartBar'

	export default{
		components:{
			percentPie,
			myLine,
			lines,
			fillWater,
			myBar,
			pieAll
		},
		data(){
			return{
				seriesData:[{
					name:"服务器总数",
					value:700
				}],
				seriesData2:[{
					name:"应用系统总数",
					value:680
				}],
				VideoTotal:[{
				    'name': '总监控数',
				    'value': 306
				},{
				    'name': '在线设备',
				    'value': 206
				},{
				    'name': '故障设备',
				    'value': 42
				}],
				FaceTotal:[{
				    'name': '监控点位',
				    'value': 58
				},{
				    'name': '在线设备',
				    'value': 48
				},{
				    'name': '异常报警',
				    'value': 23
				}],
				buildData:{
					comNum:76,
					employees:3413,
					area:9000,
					areaUsed:8750,
					total:500000,
					tax:34445
				},
				barDatas:{
					title:"",
					data:[354,544,435,856,744,562,1055],
					dataAxis:['A区','B区','C区','D区','E区','F区','G区'],
					unit:''
				},
				colorLists:['#0084ff',"#0084ff"],
				showComDetail:true,
				lineData:{
					series:[{
				    	symbol:'none',
				    	symbolSize:0,
				    	name:"下行",
				        data: [461,552,453,534,421,452],
				        type: 'line',
				        areaStyle:{
				        	normal:{
				        		color: {
								    type: 'linear',
								    x: 0,
								    y: 1,
								    x2: 0,
								    y2: 0,
								    colorStops: [{
								        offset: 0, color: 'rgba(3,92,255,.2)' // 0% 处的颜色
								    }, {
								        offset: 1, color: 'rgba(2,228,255,0.2)' // 100% 处的颜色
								    }],
								    globalCoord: false // 缺省为 false
								}
							}			
						},
						lineStyle:{
							color: {
							    type: 'linear',
							    x: 0,
							    y: 0,
							    x2: 1,
							    y2: 0,
							    colorStops: [{
							        offset: 0, color: '#02e4ff' // 0% 处的颜色
							    }, {
							        offset: 1, color: '#035cff' // 100% 处的颜色
							    }],
							    globalCoord: false // 缺省为 false
							}
						},
		        	smooth: true
			    },
					{
				    	symbol:'none',
				    	symbolSize:0,
				    	name:"上行",
				        data: [602,652,553,634,581,681],
				        type: 'line',
				        areaStyle:{
				        	normal:{
				        		show:false,
				        		color: {
								    type: 'linear',
								    x: 0,
								    y: 1,
								    x2: 0,
								    y2: 0,
								    colorStops: [{
								        offset: 0, color: 'rgba(2,255,56,0)' // 0% 处的颜色
								    }, {
								        offset: 1, color: 'rgba(2,228,255,0)' // 100% 处的颜色
								    }],
								    globalCoord: false // 缺省为 false
								}
							}			
						},
						lineStyle:{
							color: {
							    type: 'linear',
							    x: 0,
							    y: 0,
							    x2: 1,
							    y2: 0,
							    colorStops: [{
							        offset: 0, color: '#02e4ff' // 0% 处的颜色
							    }, {
							        offset: 1, color: '#02ff38' // 100% 处的颜色
							    }],
							    globalCoord: false // 缺省为 false
							}
						},
		        	smooth: true
			    }],
					dataAxis:['4/HR','8/HR','12/HR','16/HR','20/HR','24/HR'],
					unit:'单位(MB)'
				},
				lineData1:{
					dataAxis:['一月','二月','三月','四月','五月','六月','七月','八月','九月','十月'],
					unit:'(个)',
					data:[70,82,55,64,58,76,54,60,68,58]
				},
				water:[0.6,0.4,0.32],
				carData:[{name:'总带宽',value:1000,'unit':'MB'},{name:'已使用带宽',value:485,'unit':'MB'},{name:'剩余带宽',value:525,'unit':'MB'}],
				totalCar:58
				
			}

		}
	}
</script>

<style scoped="scoped">
	.line{margin-bottom: 20px;}
	.safe-width{width: 420px;
	padding-left: 50px;}
	.percent-box p{
		color: #FFFFFF;
		text-align: center;
		font-size: 16px;
	}
	.right-bg{
		padding-top: 170px;
	}

	.safe-width img{
		width: 100%;
		margin-top: 35px;
	}
	.grid{
		background: url(../assets/img/2.png)no-repeat;
		background-size: 100%;
		width: 100%;
		height: 68px;
		display: inline-block;
		margin-top: 30px;
		padding: 5px 10px;
	}
	.grid p{
		font-size: 18px;
		color: #FFFFFF;
	}
	.grid big{
		background: linear-gradient(to top, #0072ff, #00fff0);
        -webkit-background-clip: text;
		color: transparent;
		font-size: 38px;
		padding: 5px;
	}
	.safe-width h3{
		font-size:24px;
		color: #FFFFFF;
		margin-top: 50px;
		font-weight: bold;
	}
		.carData{
		color: #FFFFFF;

	}
	.carData p{
		font-size: 18px;
		height: 60px;
		text-align: left;
		vertical-align: baseline;
	}
	.carData span{
		float: right;
	}
	.carData label{
		display: inline-block;
		padding-top:15px;
		
	}
	.carData::after{
		clear: both;
	}
	.carData big{
		font-size: 40px;
		font-weight: normal;
		margin-right: 10px;
	}
	.carData .line{
		height: 3px;
		background: rgba(255,255,255,.3);
		position: relative;
	}
	.carData .line-act{
		content: "";
		position: absolute;
		top: -3px;
		height: 7px;
		width: 100%;
		animation:mymove 3s;
	    animation-iteration-count:1;
		background: -webkit-linear-gradient(left, #02e4ff , #035cff); /* Safari 5.1 - 6.0 */
	    background: -o-linear-gradient(right, #02e4ff , #035cff); /* Opera 11.1 - 12.0 */
	    background: -moz-linear-gradient(right,#02e4ff , #035cff); /* Firefox 3.6 - 15 */
	    background: linear-gradient(to right, #02e4ff , #035cff); /* 标准的语法 */
	}
	@keyframes mymove
	{
	from {width:0;}
	to {width:100%;}
	}
	
	@-webkit-keyframes mymove /*Safari and Chrome*/
	{
	from {width:0;}
	to {width:100%;}
	}
	big{
		background: linear-gradient(to top, #0072ff, #00fff0);
        -webkit-background-clip: text;
		color: transparent;
		font-family: 'DINOT';
	}
	.pm-bg{
		padding: 20px 40px;
		position: relative;
		border:2px solid rgba(13,153,242,.4);
		background: url(../assets/img/pm-bg.png)repeat;
	}
	.pm-bg ul{padding: 0;margin: 0;}
	.pm-bg li{
		width: 50%;
		display: inline-block;
		margin-left: -4px;
		color: #FFFFFF;
		font-size: 34px;
		line-height: 2em;
	}
	.pm-bg li:nth-of-type(2n){
		padding-left: 80px;
		box-sizing: border-box;
	}
	.msg{
		position: absolute;
		top: 20px;
		left: 60%;
	}
	.msg h1{
		font-size: 18px;
		color: #FFFFFF;
		font-weight: normal;
	}
	.msg p{
		margin-bottom: 20px;
	}
	.msg big{
		font-size: 36px;
		line-height: 40px;
		font-weight: bold;
	}
	.msg small{
		font-size: 18px;
		color: #32e0ff;
		margin-left: 20px;
	}
	big.green{
		background: linear-gradient(to top, #02ff38, #02e4ff);
        -webkit-background-clip: text;
		color: transparent;
	}
	.comBig{
		margin-top:30px;
	}
	.comBig big{
		font-size: 40px;
	}
	.comBig small{
		font-size: 20px;
		padding-left: 10px;
		
	}
	.dashed{position: relative;padding-left: 20px;}
	.dashed p{
		text-align: left;
	}
	.dashed:first-of-type{padding-left: 0;}
	.dashed::after{
		content: "";
		position: absolute;
		right: 0;
		height: 80%;
		top:15%;
		border-right: 2px dashed rgba(255,255,255,.3);
	}
	.dashed:last-of-type::after{
		border: none;
	}
	.dashed h1{
		color: #FFFFFF;
		font-size: 18px;
		font-weight: normal;
	}
	.error small{
			color: #e5210f;
						
		}
	.error big{

		background: linear-gradient(to top, #e10019, #ff4e00);
        -webkit-background-clip: text;
		color: transparent;
		font-family: 'DINOT';

	}
	.wltj{
		border-bottom: 1px solid rgba(255,255,255,.2);
		height: 60px;
	}
	.wltj label{
		color: #98d2ff;
		font-size: 24px;
		line-height: 60px;
		margin-top: 5px;
		display: inline-block;
	}
	.wltj span{
		float: right;
		color: #fff;
	}
	.wltj big{
		font-size: 40px;
		font-weight: bold;
		padding: 0 5px;
	}
</style>


