<template>
	<div class="p index-bg">
		<bg v-if="$route.path=='/'"></bg>
		<numbers v-if="$route.path=='/'"></numbers>
		<div class="house">
			<div class="line-box">
				<div class="left-line-1"></div>
				<div class="left-line-2"></div>
				<div class="left-line-3"></div>
				<div class="left-line-4"></div>
				<div class="right-line-1"></div>
				<div class="right-line-2"></div>
				<div class="right-line-3"></div>
				<div class="right-line-4"></div>
			</div>
			<img src="../assets/img/bg.png"/>
		</div>
		
		<el-container class="Home" style="min-height: 100%;">
			<div class="header">
				<img src="../assets/img/logo.png" />
			</div>
			<div class="index-title">
				<img src="../assets/img/title.png" />
			</div>
			<div class="center-bg-chanye" @click="routerTo('/industry')"></div>
			<div class="center-bg-yuanqu" @click="routerTo('/park')"></div>
			<div class="menu-btn">
				<ul>
					<li><i class="btn-indexBtn btn-yw" @click="routerTo('/operations')" /></li>
					<li style="margin-top: 100px;"><i class="btn-indexBtn btn-safe" @click="routerTo('/safe')" /></li>
					<li style="margin-top: 200px;"><i class="btn-indexBtn btn-wl"  @click="routerTo('/property')" /></li>
					<li style="margin-top: 240px;"><i class="btn-indexBtn btn-kf"  @click="routerTo('/customer')" /></li>
					<li style="margin-top: 200px;"><i class="btn-indexBtn btn-ggjs" @click="routerTo('/technology')"/></li>
					<li style="margin-top: 100px;"><i class="btn-indexBtn btn-pptg" @click="routerTo('/extend')" /></li>
					<li><i class="btn-indexBtn btn-rcfw" @click="routerTo('/server')" /></li>
				</ul>
			</div>
		</el-container>
	</div>
</template>

<script>
	import bg from '@/components/module/index/bg'
	import Numbers from '@/components/module/index/number'
//	企业详请
export default {
  components: {bg,Numbers},
  data() {
    return {
      showIndex: true
    };
  },
  mounted() {
    // var proxy = this.$proxy;
	  // setTimeout(() => {
	  // 	proxy.invoke('send',888);
	  // }, 2000);
  },
  methods: {
    routerTo(path) {
      this.$router.push(path);
    }
  }
};
</script>

<style scoped="scoped" lang="less">
.index-bg {
  background: url(../assets/img/bg.png) no-repeat bottom;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: relative;
}
.index-title {
  position: relative;
  left: 50%;
  margin-left: -450px;
}

.menu-btn {
 	width: 80%;
    height: 35%;
    left: 10%;
    top: 50%;
    position: absolute;
  ul {
    display: flex;
  }

  li {
    flex: 1;
    display: inline-block;
    width: 220px;
    text-align: center;
  }
  img {
    width: 220px;
    height: 210px;
    margin: 0 20px;
    cursor: pointer;
  }
}
.house{
	 position: absolute; bottom: 0;left: 0;width: 100%;height: 100%;text-align: center;
	img{
		 position: absolute; bottom: 0;left: 0;width: 100%;
	}
	.left-line-1{
		background:url(../assets/img/index/line-l-1.png)no-repeat right;
		width: 125px;
		height:0;
		position: absolute;
		bottom: calc(85% - 525px);
		left: 25%;
		animation: myfirst 4.5s linear 2s alternate infinite;
	}
	.left-line-2{
		background:url(../assets/img/index/line-l-2.png)no-repeat right;
		width: 125px;
		height:0;
		position: absolute;
		bottom: calc(82% - 525px);
		left: 29%;animation: myfirst 4s linear 2s alternate infinite;
	}
	.left-line-3{
		background:url(../assets/img/index/line-l-2.png)no-repeat right;
		width: 125px;
		height:0;
		position: absolute;
		bottom: calc(82% - 525px);
		left: 38%;animation: myfirst 3.3s linear 2s alternate infinite;
	}
	.left-line-4{
		background:url(../assets/img/index/line-center.png)no-repeat right;
		width: 125px;
		height:0;
		position: absolute;
		bottom: calc(80% - 525px);
		left: 42%;animation: myfirst 4.1s linear 2s alternate infinite;
	}
	.right-line-1{
		background:url(../assets/img/index/line-r-1.png)no-repeat right;
		width: 125px;
		height:0;
		position: absolute;
		bottom: calc(80% - 525px);
		left: 53%;animation: myfirst 4s linear 2s alternate infinite;
	}
	.right-line-2{
		background:url(../assets/img/index/line-r-1.png)no-repeat right;
		width: 125px;
		height:0;
		position: absolute;
		bottom: calc(82% - 525px);
		left: 57%;animation: myfirst 3.3s linear 2s alternate infinite;
	}
	.right-line-3{
		background:url(../assets/img/index/line-r-3.png)no-repeat right;
		width: 125px;
		height:0;
		position: absolute;
		bottom: calc(82% - 525px);
		left: 61%;animation: myfirst 4.5s linear 2s alternate infinite;
	}
	.right-line-4{
		background:url(../assets/img/index/line-r-4.png)no-repeat right;
		width: 125px;
		height:0;
		position: absolute;
		bottom: calc(85% - 525px);
		left: 66%;animation: myfirst 3s linear 2s alternate infinite;
	}
}
@keyframes myfirst
{
from {height: 0;}
to {height: 525px;}
}
.line-box{
    position: relative;
    margin: 0 auto;
    display: inline-block;
    width: 1600px;
    height: 525px;
}
</style>


