<template>
	<div style="height:100%;" :id="ids">

	</div>
</template>

<script>
	export default{
		props:{
			ids:{
				type:String,
				default:"bar"
			},
			data:{
				default:{
					title:"",
					data:[],
					dataAxis:[]
				}
			},
			unit:{
				default:""
			}
		},
		data(){
			return{
				
			}
		},
		methods:{
			setEchartsPie(id, scaleData) {
				var rich = {
				    big: {
				        color: '#57b8ff',
				        align: 'center',
				        padding: [5, 0,20,0],
				        fontFamily:'DINOT',
				        fontSize:36
				    },
				    small:{
				    	color: '#57b8ff',
				        align: 'center',
				        padding: [5, 0,30,10],
				        fontSize:16
				    },
				    name:{
				    	color: '#fff',
				        fontSize:18
				    }
				};
				var placeHolderStyle = {
				    normal: {
				        label: {
				            show: false
				        },
				        labelLine: {
				            show: false
				        },
				        color: 'rgba(0, 0, 0, 0)',
				        borderColor: 'rgba(0, 0, 0, 0)',
				        borderWidth: 0
				    }
				};
				var data = [];
				for (var i = 0; i < scaleData.length; i++) {
				    data.push({
				        value: scaleData[i].value,
				        name: scaleData[i].name,
				        itemStyle: {
				            normal: {
				                borderWidth: 6,
				                borderColor: new this.$echarts.graphic.LinearGradient(0, 0, 1, 1, [{
				                    offset: 0,
				                    color: '#006cff'
				                }, {
				                    offset: .41,
				                    color: '#00f0ff'
				                }, {
				                    offset: .81,
				                    color: '#012647'
				                }, {
				                    offset: .98,
				                    color: '#000'
				                }]),
				                shadowColor: 'rgba(142, 152, 241, 0.6)'
				            }
				        }
				    }, {
				        value: 5,
				        name: '',
				        itemStyle: placeHolderStyle
				    });
				}
				var seriesObj = [{
				    name: '',
				    type: 'pie',
				    clockWise: false,
				    center:['27%','50%'],
				    radius: ['70%', '71%'],
				    hoverAnimation: false,
				    startAngle:0,
				    itemStyle: {
				        normal: {
				            label: {
				                show: true,
				                position: 'center',
				                color: '#ddd',
				                formatter: (params)=> {
				                    if(params.name !== '') {
				                        return '{big|'+params.value+'}{small|'+this.unit+'}\n'+'{name|'+params.name+'}';
				                    }else {
				                        return '';
				                    }
				                },
				                rich: rich
				            },
				            labelLine: {
				                show: false
				            }
				        }
				    },
				    data: data
				}];
				let option = {
					tooltip: {
						trigger: 'item',
						show:false
					},
					legend: {
						show:false,
					},
					series: seriesObj,
					color: ['#658ef0', '#ffb966', '#65dfa4', '#79c3ff', '#e479ff', '#ec9be0', '#f2f049', '#89f065']
				};
				var myChartPie = this.$echarts.init(document.getElementById(id));
				myChartPie.setOption(option);
			}
			},
		watch:{
			
		},
		created(){
			
		},
		mounted(){
			this.setEchartsPie(this.ids, this.data);
		},
		destoryed(){
			
		}
	}
</script>

<style>
</style>


