<template>
	<div class="p">

	

		<div class="left-bg">
			<indexLeft v-if="actPage==0"></indexLeft>
			<piciDetail :comData='buildData' v-else-if="actPage==1"></piciDetail>
			<yuanqu-detail :comData='buildData' v-else-if="actPage==2"></yuanqu-detail>
			<yuanqu-detail :comData='buildData' v-else-if="actPage==3"></yuanqu-detail>
			<company-detail :comData='companyData' v-else-if="actPage==4"></company-detail>
		</div>
		<div class="right-bg">
			<indexRight  v-if="actPage==0"></indexRight>
			<pici-total v-else-if="actPage==1"></pici-total>
			<yuanqu-total  v-else-if="actPage==2"></yuanqu-total>
			<build-total  v-else-if="actPage==3"></build-total>
			<company-total  v-else-if="actPage==4"></company-total>
		</div>
	</div>	
</template>

<script>
//	企业详请
	import companyDetail from './module/chanye/companyDetail'
	import companyTotal from './module/chanye/companyTotal'
//具体楼栋
	import buildDetail from './module/chanye/buildDetail'
	import buildTotal from './module/chanye/buildRight'
//园区、批次总览
	import yuanquDetail from './module/chanye/yuanquDetail'
	import yuanquTotal from './module/chanye/yuanquTotal'
	//园区、批次总览
	import piciDetail from './module/chanye/piciDetail'
	import piciTotal from './module/chanye/piciTotal'
//总体
	import indexLeft from './module/chanye/indexLeft'
	import indexRight from './module/chanye/indexRight'
	

	export default{
		components:{
			companyDetail,
			companyTotal,
			buildDetail,
			buildTotal,
			yuanquDetail,
			yuanquTotal,
			indexLeft,
			indexRight,
			piciDetail,
			piciTotal,
		
		},
		data(){
			return{
				companyData:{
					'name':'腾讯计算机系统有限公司',
					'registTime':'成立于1998年11月',
					'startTime':'2015年10月20日',
					'area':23450,
					'employees':'50000+',
					'type':'互联网综合',
					'code':'91440300708461136T',
					'risk':'行政处罚24件',
					'money':'6500万元人民币'
				},
				buildData:{
					comNum:76,
					employees:3413,
					area:9000,
					areaUsed:8750,
					total:500000,
					tax:34445
				},
				actPage:0
				
			}

		},
		methods:{
			changePage(value){//value 0:总览 1：批次 2：园区 3：楼栋 4：企业
				this.actPage=value;
			}
		}
	}
</script>
<!--
<style scoped>
	.p{
		width: 100%;
		height:100%
	}
</style>-->


