<template>
	<div @click="showYuanquMenu=false" style="height: 100%;">
		

	<el-container class="Home" style="min-height: 100%;">
			<div class="header">
				<img src="../assets/img/logo.png" @click="routerTo('/industry')"/><span @click="showCircleFly">{{$route.meta.modelName}}</span>
			</div>
			<div class="weather"><big>/10°</big><span>{{'2018年10月14日' | getDay}}</span></div>
			<el-main class="main">
					<map3D ref="map"></map3D>
				<div class="bottom-bg">
				
				
					
				</div>
					<div class="z99 bottom-bg">
						
						<ul v-if="$route.meta.menu=='other'" class="other">
							<li @click="routerTo('/')" >
								<i class="my-icon my-icon-home"></i>
								<p style="margin-top: -1.3em;">首页</p>
							</li>
							<li v-for="item in othermenu" @click.stop="routerToSub(item.path)" :class="{active:$route.path==item.path}">
								<i class="my-icon" :class="item.icon"></i>
							</li>
						</ul>
						<ul v-else-if="$route.meta.menu=='park'">
							<li @click.stop="routerTo('/park')" style="cursor: pointer;">
								<i class="my-icon my-icon-yuanqu"></i>
								<p>园区大数据</p>
							</li>
							<li v-for="item in parkmenu" @click.stop="routerToSub(item.path)" :class="{active:$route.path==item.path}">
								<i class="my-icon" :class="item.icon"></i>
							</li>
						</ul>
						<ul v-else>
							<li @click="routerTo('/')" >
								<i class="my-icon my-icon-home"></i>
								<p>首页</p>
							</li>
							<li @click="routerTo('/industry')"  :class="{active:$route.meta.modelName=='产业大数据'}">
								<i class="my-icon my-icon-changye"></i>
								<p>产业大数据</p>
							</li>
							<li @click.stop="routerTo('/park')" :class="{active:$route.meta.modelName=='园区大数据'}">
								<i class="my-icon my-icon-yuanqu"></i>
								<p>园区大数据</p>
								<div class="sub-bg" v-if="showYuanquMenu">
									<div class="subMenus" :class="showYuanqu?'sub-menu':'hide-menu'">
										<ul>
											<li class="fisrt-menu"  @click.stop="routerToSub('/traffic')">交通体系</li>
											<li class="second-menu"  @click.stop="routerToSub('/pay')">支付体系</li>
											<li class="three-menu" @click.stop="routerToSub('/coreSys')">核心系统</li>
											<li class="four-menu" @click.stop="routerToSub('/warning')">预警系统</li>
											<li class="four-menu" @click.stop="routerToSub('/flood')">防汛情况</li>
											<li class="four-menu" @click.stop="routerToSub('/construction')">装修施工</li>
											<li class="four-menu" @click.stop="routerToSub('/run')">运营商情况</li>
										</ul>
									</div>
								</div>
							</li>
						</ul>
					</div>
				<keep-alive>
					<router-view v-if="$route.meta.keepAlive">
						<!-- 这里是会被缓存的视图组件 -->
					</router-view>
				</keep-alive>

				<router-view v-if="!$route.meta.keepAlive" ref="content">
					<!-- 这里是不被缓存的视图组件 -->
				</router-view>

			</el-main>

	</el-container>
	</div>
	
</template>

<script>
	import map3D from './maps/map3D'
export default {
  name: 'HelloWorld',
  data () {
    return {
    	// showYuanqu:false,
    	// showSubMenu:false,
    	// showYuanquMenu:false,
    	parkmenu:[{
    		icon:'my-icon-traffic',//智慧交通
    		path:'/traffic'
    	},{
    		icon:'my-icon-pay',//支付
    		path:'/pay'
    	},{
    		icon:'my-icon-coreSys',//核心系统
    		path:'/coreSys'
    	},{
    		icon:'my-icon-warning',//预警
    		path:'/warning'
    	},{
    		icon:'my-icon-Flood',//防汛
    		path:'/flood'
    	},{
    		icon:'my-icon-construction',//装修施工
    		path:'/construction'
    	},{
    		icon:'my-icon-run',//运行情况
    		path:'/run'
    	}],
    	othermenu:[{
    		icon:'my-icon-safe',//智慧安防
    		path:'/safe'
    	},{
    		icon:'my-icon-customer',//客服
    		path:'/customer'
    	},{
    		icon:'my-icon-operations',//运维
    		path:'/operations'
    	},{
    		icon:'my-icon-property',//物联
    		path:'/property'
    	},{
    		icon:'my-icon-technology',//公共技术
    		path:'/technology'
    	},{
    		icon:'my-icon-extend',//品牌推广
    		path:'/extend'
    	},{
    		icon:'my-icon-server',//人才服务
    		path:'/server'
    	}]
    }
  },
  components:{
	  map3D
  },
  filters:{
  	getDay(val){
  		let date=new Date();
  		return date.getFullYear()+'年'+(date.getMonth()+1)+'月'+date.getDate()+'日';
  	}
  },
    mounted() {
    this.checkRoutTo(this.$route.fullPath)
  },
  methods:{
  	routerTo(path){
		this.showYuanquMenu=false;
		  this.$router.replace(path)
		  this.checkRoutTo(path);
  	},
  	routerToSub(path){
  		this.$router.push(path)
		this.showYuanquMenu=false;
		this.checkRoutTo(path);		 
	  },
	  goToChanye(){
		  if(this.$refs.content.changePage)
		  this.$refs.content.changePage(0)
		  this.$refs.map.loadchanye();
		
	  },
	  checkRoutTo(path){
		  if(path=='/park'){
				this.showYuanquMenu=true;
				this.$refs.map.loadquanjing();
		  }
		 else if(path=="/industry"){
			  this.goToChanye();
			  
		  } 
		  else if(path=="/traffic"){
			this.$refs.map.loadDaoza()
		  }
		  else if(path=="/warning"){
			this.$refs.map.loadAlarmCircle()
		  }
		   else if(path=="/coreSys"){
			this.$refs.map.loadAlarmSysCircle()
		  } else if(path=="/safe"){
			this.$refs.map.loadAnfangVideo();
			this.$refs.map.loadBaoanRout();
		  }
		  else{
			  this.$refs.map.clearDataSourcees();
			  this.$refs.map.goHome()
		  }
	  },
	  showCircleFly(){
		    this.$refs.map.doCirCleFly()
	  }
  	
  },
  watch:{
  	showYuanqu(val){

		setTimeout(()=>{
			this.showSubMenu=val
		},1000)

  	}
  }
}
</script>
<style scoped="scoped" lang="less">
	.z99{
		z-index: 999;
		 height: 180px;
	    bottom: 0;
	    left: 20%;
	    width: 60%;
	    position: absolute;
	    background: transparent;
	}
	.hideSub{
		display: none;
	}
	.sub-bg{
		position: absolute;
		/*width: 600px;*/
		height: 45px;
		bottom: 110px;
		left: -325px;
		overflow: hidden;
		line-height: 40px;
		z-index: -1;
	}
	.subMenus{
		/*background: url(../assets/img/sub-menu-bg.png)no-repeat bottom center;*/
		/*background-size: 100% 100%;*/
		/*width: 730px;*/
		/*height: 70px;*/
		/*bottom: 0;
		position: /*absolute*/
		display: block;
		color: #FFFFFF;
		white-space: nowrap;
	}
	.hide-menu{
		/*transform: rotate(-180deg);
		-webkit-transition-property: -webkit-transform;
	    -webkit-transition-duration: 1s;
	    -moz-transition-property: -moz-transform;
	    -moz-transition-duration: 1s;
	    -webkit-animation: rotateOut 1s linear ;
	    -moz-animation: rotateOut 1s linear ;
	    -o-animation: rotateOut 1s linear ;
	    animation: rotateOut 1s linear ;
	    -webkit-transform-origin: center bottom;
		transform-origin: center bottom;*/
		
		
	}
	.sub-menu{
		/*-webkit-transition-property: -webkit-transform;
	    -webkit-transition-duration: 1s;
	    -moz-transition-property: -moz-transform;
	    -moz-transition-duration: 1s;
	    -webkit-animation: rotate 1s linear ;
	    -moz-animation: rotate 1s linear ;
	    -o-animation: rotate 1s linear ;
	    animation: rotate 1s linear ;
	    -webkit-transform-origin: center bottom;
		transform-origin: center bottom;
		transform: rotate(0deg);*/
	}
	.sub-bg{
		background: url(../assets/img/sub-bg.png)no-repeat;	
		padding: 0 6px;
		background-size: 100% 100%;
		ul{
			position: initial;
			height: 100%;
			font-size: 24px;
			li{
				
		   		 /*position: absolute;
				width: 120px;*/
				    margin: 0 5px;
					    position: relative;
					    font-size: 18px;
			}
			li:hover{
			    background: -webkit-gradient(linear, left bottom, left top, from(#0072ff), to(#00c0ff));
			    background: linear-gradient(to top, #0072ff, #00c0ff);
			    -webkit-background-clip: text;
			    color: transparent;
			}
			li::after{
				content: "";
				position: absolute;
				right: -8px;
				height: 20px;
				width: 1px;
				top: 10px;
				background: url(../assets/img/sub-line.png);
			}
			li:last-of-type:after{
				background: transparent;
				width: 0;
			}
		}


	}
	@-webkit-keyframes rotate{from{-webkit-transform: rotate(180deg)}
	    to{-webkit-transform: rotate(0deg)}
	}
	@-moz-keyframes rotate{from{-moz-transform: rotate(180deg)}
	    to{-moz-transform: rotate(0deg)}
	}
	@-o-keyframes rotate{from{-o-transform: rotate(180deg)}
	    to{-o-transform: rotate(0deg)}
	}
	@keyframes rotate{from{transform: rotate(180deg)}
	    to{transform: rotate(0deg)}
	}
	
	
	@-webkit-keyframes rotateOut{from{-webkit-transform: rotate(0deg);}
	    to{-webkit-transform: rotate(-180deg);}
	}
	@-moz-keyframes rotateOut{from{-moz-transform: rotate(0deg);}
	    to{-moz-transform: rotate(-180deg)}
	}
	@-o-keyframes rotateOut{from{-o-transform: rotate(0deg);}
	    to{-o-transform: rotate(-180deg);}
	}
	@keyframes rotateOut{from{transform: rotate(0deg);}
	    to{transform: rotate(-180deg);}
	}
	.other{
		.my-icon{
			height: 100px;
		}
	}
</style>



