		color: transparent;
		font-size: 38px;
		padding: 5px;
	}
	.safe-width h3{
		fonts-size:24px;
		color: #FFFFFF;
		margin-top: 50px;
		font-weight: bold;
	}
	.percent-box{
		img{
		    margin-top: 2px;
		    height: 100px;
		}
