<template>
	<div class="right-park-total ">
		<img src="../../../assets/img/title/hx-sdq.png"/>
		<div class="warn-list" :class="item.icon" v-for="(item,index) in sysData" :key="index">
			<label>{{item.title}}</label>
			<span><big>{{item.data}}</big><small>{{item.unit}}</small></span>
		</div>
		<img src="../../../assets/img/title/hx-ysl.png" class="img-title"/>
		<h3 style="position: absolute;right: -30px;margin-top: -10px;z-index: 999;">
		<el-popover placement="bottom" v-model="showEnergy" width="60" trigger="click" class="myPopover">
			  <div class="drap-down" slot="reference">{{energy}}&nbsp;&nbsp;<i class="el-icon-arrow-down"></i></div>
			  <div class="drap-option">
			  	<ul>
			  		<li @click="energy='水';showEnergy=false">水</li>
			  		<li @click="energy='电';showEnergy=false">电</li>
			  		<li @click="energy='气';showEnergy=false">气</li>
			  	</ul>
			  </div>
			</el-popover>
		</h3>
		<div class="echartline">
			<myBar ids="bar2" ref="bar2" v-if="showBar" :data="barDatas" :color="colorLists"></myBar>
		</div>

		
		<div class="warning">
			<img src="../../../assets/img/title/hx-xfyj.png" class="img-title"/>
			
			<div class="warn-list" :class="item.icon" v-for="(item,index) in warnData" :key="index">
				<label>{{item.title}}</label>
				<span>{{item.status}}</span>
			</div>
		</div>
	</div>
	
</template>

<script>
	import percentPie from './../echarts/percentPie'
	import myBar from '@/components/module/echarts/echartBar.vue'
	export default{
		components:{
			percentPie,
			myBar
		},
		data(){
			return{
				sysData:[{
					icon:"icon-water-act",
					title:'水使用量',
					unit:'M³',
					data:850
				},{
					icon:"icon-electricity-act",
					title:'电使用量',
					unit:'KW·H',
					data:654
				},{
					icon:"icon-gas-act",
					title:'气使用量',
					unit:'NM³',
					data:120
				}],
				warnData:[{
					icon:"icon-user-act",
					title:'连续安全天数',
					status:'28天'
				},{
					icon:"icon-earth-act",
					title:'消防设备运行情况',
					status:'正常'
				}],
				barDatas:{
					title:"",
					data:[382,485,458,364,342,383],
					dataAxis:['A区','B区','C区','D区','E区','G区'],
					unit:''
				},
				colorLists:['#0084ff',"#0084ff"],
				showYear:false,
				showEnergy:false,
				showWarn:false,
				year:'2018年',
				energy:"水",
				warning:'2018年',
				showBar:true
			}
		},
		watch:{
			energy(data){
				this.showBar=false;
				this.$nextTick(()=>{
					this.showBar=true;
				})
			}
		}
	}
</script>

<style type="text/css" scoped="scoped">
	.echartline{
		height: 250px;
		width: 100%;
	}
	.warn-list::after{
		top: 24px;
	}
	.right-park-total{padding-top: 0;margin-left: 20px;padding-right:none;float: none;}
	
</style>


