<template>
	<div class="company">
		<div class="comMsg">
			<div class="buildMsg">

				<p><span>双创企业数量</span><label>{{comData.company}}</label><small>+</small></p>
				<p><span>电使用量</span><label>{{comData.electricity}}</label><small>KW·H</small></p>
				<p><span>气使用量</span><label>{{comData.gas}}</label><small>M³</small></p>
			</div>
		</div>
		<div class="echartline" >
			<h3>双创企业类别</h3>
			<myBar ids="barType" ref="barType" :data="barDatas" :color="colorLists"></myBar>			
		</div>
		<div class="echartline right-park">
			<h3>累计项目统计</h3>
			<myBarCircle ids="myBarCircle" ref="myBarCircle" :data="seriesData"></myBarCircle>
			<ul class="lenged">
				<li v-for="(item,index) in seriesData.data" :key="index"><p>{{item.name}}</p><p><big>{{item.value}}</big><small>个</small></p></li>
			</ul>
		</div>
	</div>
</template>

<script>

	import myBar from '@/components/module/echarts/bar.vue'
	import myBarCircle from '@/components/module/echarts/pieCircle'
	export default{
		components:{
			myBar,
			myBarCircle
		},
		data(){
			return{
				barDatas:{
					title:"",
					data:[7,3,6,5,3,3],
					dataAxis:['金融','IT','服务','教育','硬件','旅游'],
					unit:''
				},
				seriesData:{
					lenged: ['现孵化团队','已孵化团队'],
					data: [{
						value: 969,
						name: '已孵化团队',
						itemStyle:{
							color:{
								type: 'linear',
							    x: 0,
							    y: 0,
							    x2: 0,
							    y2: 1,
							    colorStops: [{
							        offset: 0, color: '#2ed9ff' // 0% 处的颜色
							    },{
							        offset: 1, color: '#047cff' // 100% 处的颜色
							    }],
							    globalCoord: false // 缺省为 fals
							}
						}
					},{
						value: 254,
						name: '现孵化团队',
						itemStyle:{
							color:{
								type: 'linear',
							    x: 0,
							    y: 0,
							    x2: 0,
							    y2: 1,
							    colorStops: [{
							        offset: 0, color: '#3cff00' // 0% 处的颜色
							    },{
							        offset: 1, color: '#00b72b' // 100% 处的颜色
							    }],
							    globalCoord: false // 缺省为 fals
							}
							
						}
					}],
					name: '累计入驻项目'
				},
				
				comData:{
					company:200,
					electricity:50001,
					gas:1555

				},
				colorLists:['#0084ff',"#0084ff"],
				indexList:[{
					title:'单日用水量',
					icon:'icon-water',
					unit:'M³',
					data:60
				},{
					title:'单日用电量',
					icon:'icon-electricity',
					unit:'M³',
					data:600
				},{
					title:'单日用气量',
					icon:'icon-gas',
					unit:'NM³',
					data:600
				},{
					title:'消防报警',
					icon:'icon-fire',
					unit:'次',
					data:6
				},{
					title:'敏感人群',
					icon:'icon-sensitive',
					unit:'个',
					data:6
				},{
					title:'电梯运行情况',
					icon:'icon-elevator small',
					unit:'',
					data:'正常'
				}]
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-left: 40px;

		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& h3,label{
			font-size: 24px;
		}
		& span{
			font-size: 20px;
		}
		& p{
			margin: 15px 0;
		}
	}
	.echartline{
		height: 250px;
		width: 480px;
		position: relative;
	}
	.buildMsg{
		width: 480px;
		p{border-bottom: 2px solid rgba(255,255,255,.24);text-align: right;}
		& span{
			width: 10em;
			font-size: 22px;
			display: inline-block;
			text-align: left;
	        margin-top:10px;
			float: left;
		}
		& label{
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        font-size: 40px;
			width: 3em;
			display: inline-block;
			font-weight: bold;
			text-align: left;
			font-family: 'DINOT';
		}
		& small{
			font-size: 20px;
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        width: 3em;
	        display: inline-block;
	        text-align: right;
	        float: right;
	        margin-top:18px;
	        font-family: 'DINOT';
	        font-weight: bold;
		}
		
	}
	.lenged{
		position: absolute;
		right: 30px;
		top: 100px;
		width: 100px;
		li:first-of-type big{ 
			 background: linear-gradient(to top, #00b72b, #3cff00);
	        -webkit-background-clip: text;
	        color: transparent;
		}
		p{
			margin: 0;
		}
	}
	
</style>


