import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import chanye from '@/components/chanye'
import park from '@/components/park'
import coreSys from '@/components/coreSystem'
import shuangchuang from '@/components/shuangchuang'
import safe from '@/components/safe'
import operations from '@/components/operations'
import extend from '@/components/extend'
import index from '@/components/index'
import map from '@/components/maps/map3D'

Vue.use(Router) //
export default new Router({
        routes:
        [{
            path:
            '/',
            name: '首页',
            component: index,
            meta: {
                modelName: "首页"
            },
        },
        {
            path: '/industry',
            name: 'Home',
            component: Home,
            meta: {
                modelName: "天府软件园大数据平台"
            },
            children: [

            {
                path: '/industry',
                name: '产业大数据',
                component: chanye,
                meta: {
                    modelName: "产业大数据"
                },
            },
            {
                path: '/park',
                name: '园区大数据',
                component: park,
                meta: {
                    modelName: "园区大数据"
                },
            },
            {
                path: '/coreSys',
                name: '核心系统',
                component: coreSys,
                meta: {
                    modelName: "核心系统",
                    menu: 'park'
                },
            },
            {
                path: '/traffic',
                name: '智慧交通',
                component: resolve =>{
                    require(['@/components/traffic'], resolve)
                },
                meta: {
                    modelName: "智慧交通",
                    menu: 'park'
                }
            },
            {
                path: '/warning',
                name: '预警系统',
                component: resolve =>{
                    require(['@/components/warning'], resolve)
                },
                meta: {
                    modelName: "预警系统",
                    menu: 'park'
                }
            },
            {
                path: '/yunwei',
                name: '运维情况',
                component: operations,
                meta: {
                    modelName: "运维情况",
                    menu: 'park'
                }

            },
            {
                path: '/construction',
                name: '装修施工',
                component: resolve =>{
                    require(['@/components/construction'], resolve)
                },
                meta: {
                    modelName: "装修施工",
                    menu: 'park'
                }
            },
            {
                path: '/run',
                name: '运营商情况',
                component: resolve =>{
                    require(['@/components/run'], resolve)
                },
                meta: {
                    modelName: "运营商情况",
                    menu: 'park'
                }
            },
            {
                path: '/pay',
                name: '支付体系',
                component: resolve =>{
                    require(['@/components/pay'], resolve)
                },
                meta: {
                    modelName: "支付体系",
                    menu: 'park'
                }
            },
            {
                path: '/flood',
                name: '防汛情况',
                component: resolve =>{
                    require(['@/components/fangxun.vue'], resolve)
                },
                meta: {
                    modelName: "防汛情况",
                    menu: 'park'
                }
            },
            {
                path: '/shuangchuang',
                name: '双创大数据',
                component: shuangchuang,
                meta: {
                    modelName: "双创大数据"
                },
            },
            {
                path: '/safe',
                name: '智慧安防',
                component: safe,
                meta: {
                    modelName: "智慧安防",
                    menu: 'other'
                },
            },
            {
                path: '/operations',
                name: '智能运维',
                component: operations,
                meta: {
                    modelName: "智能运维",
                    menu: 'other'
                }

            },
            {
                path: '/extend',
                name: '品牌推广',
                component: extend,
                meta: {
                    modelName: "品牌推广",
                    menu: 'other'
                }

            },
            
            {
                path: '/server',
                name: '人才服务',
                component: resolve =>{
                    require(['@/components/server.vue'], resolve)
                },
                meta: {
                    modelName: "人才服务",
                    menu: 'other'
                }
            },
            {
                path: '/technology',
                name: '公共技术',
                component: resolve =>{
                    require(['@/components/technology.vue'], resolve)
                },
                meta: {
                    modelName: "公共技术",
                    menu: 'other'
                }
            },
            {
                path: '/property',
                name: '智能物联',
                component: resolve =>{
                    require(['@/components/property.vue'], resolve)
                },
                meta: {
                    modelName: "智能物联",
                    menu: 'other'
                }
            },
            {
                path: '/customer',
                name: '智慧客服',
                component: resolve =>{
                    require(['@/components/customer.vue'], resolve)
                },
                meta: {
                    modelName: "智慧客服",
                    menu: 'other'
                }
            }

            ]
        },
        {
            path: '/map',
            name: 'map',
            component: map,
            meta: {
                modelName: "ditu"
            }
        }]
    })


