<template>
	<div class="company">
		<div class="comMsg">
			<img src="../../../assets/img/title/hx-dtgz.png"/>
			<div style="height: 280px;">
				<myBar ids="projNum" ref="projNum" :data="barDatas" :color="colorLists"></myBar>		
				
			</div>

			
		</div>
		<div class="echartline" >
			<img src="../../../assets/img/title/hx-mgrq.png" class="img-title"/>
			<el-row :gutter="10" class="percent-box" style="margin-top: 20px;">
				<el-col :span="12">
					<img src="@/assets/img/safeFace1.jpg"/>
				</el-col>
				<el-col :span="12">
					<img src="@/assets/img/safeFace2.jpg"/>
				</el-col>
				
				</el-row>
			<el-row :gutter="10" class="percent-box" style="margin-top: 50px;">
				<el-col :span="8" v-for="(item,index) in FaceTotal" :key="index">
					<div style="height: 125px;">
						<circleHalf :ids="'face'+index" :data='[item]' :total="58"> </circleHalf>
					</div>
					<p style="text-align: center;margin: 0;">{{item.name}}</p>
				</el-col>
			</el-row>
		</div>
		
	</div>
</template>

<script>

	import myBar from '@/components/module/echarts/echartBarDD.vue'
	import circleHalf from '@/components/module/echarts/circleHalf'
	export default{
		components:{
			myBar,
			circleHalf
		},
		data(){
			return{
				barDatas:{
					title:"",
					data:[[25,35,16,18,23,26],[2,1,0,0,1,2]],
					dataAxis:['A区','B区','C区','E区','F区','G区'],
					unit:'',
					legend:['总数','故障']
				},
				comData:{
					comTotal:8567,
					regist:356,
					serve:6451

				},
				FaceTotal:[{
				    'name': '当日捕捉数',
				    'value': 10
				},{
				    'name': '当月捕捉数',
				    'value': 286
				},{
				    'name': '当年捕捉数',
				    'value': 896
				}],
				colorLists:['#0084ff',"#0084ff"],
				
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-top: 170px;
		margin-right: 40px;
		float: right;
		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& h3,label{
			font-size: 24px;
		}
		& span{
			font-size: 20px;
		}
		& p{
			margin: 15px 0;
		}
	}
	.echartline{
		height: 250px;
		width: 100%;
	}
	.buildMsg{
		width: 100%;
		p{border-bottom: 2px solid rgba(255,255,255,.24);text-align: right;}
		& span{
			width: 10em;
			font-size: 22px;
			display: inline-block;
			text-align: left;
	        margin-top:10px;
			float: left;
		}
		& label{
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        font-size: 40px;
			width: 3em;
			display: inline-block;
			font-weight: bold;
			text-align: left;
			font-family: 'DINOT';
		}
		& small{
			font-size: 20px;
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        width: 3em;
	        display: inline-block;
	        text-align: right;
	        float: right;
	        margin-top:10px;
	        font-family: 'DINOT';

		}
	}
	.percent-box{
		img{width: 100%;}
	}
</style>


