<template>
	<div style="height:100%;" :id="ids">

	</div>
</template>

<script>
	export default{
		props:{
			ids:{
				type:String,
				default:"bar"
			},
			data:{
				default:{
					title:"",
					data:[],
					dataAxis:[]
				}
			},
		},
		data(){
			return{
				
			}
		},
		methods:{
			setEchartsPie(id, seriesData) {
				let option = {
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b}: {c} 万元({d}%)"
					},
					legend: {
						show:false,
						orient: 'vertical',
						x2: 20,
						y: 'bottom',
						data: seriesData.legend,
						itemWidth: 10,
						itemHeight: 10
					},
					series: [{
						hoverOffset:0,
						name: seriesData.name,
						type: 'pie',
						center: ['25%', '50%'],
						radius: ['70%', '80%'],
						avoidLabelOverlap: false,
						startAngle:0,
						label: {
							normal: {
								show: true,
								position: 'center',
								color:'#ffff',
								fontSize:18,
//								formatter:"\n \n \n \n \n{a}"
								 formatter: [
								        '{a|1223}{b|个}',
								        '{x|{a}}'

								    ].join('\n'),
								
								    rich: {
								        a: {
								            color: '#047cff',
								            lineHeight: 70,
								            fontSize:48,
								            fontWeight:'bold',
								            fontFamily:'DINOT'
								        },
								        b: {
								        	color:"#fff",
											fontSize: 16,
											padding:[0,0,10,5]
								        },
								        x: {
								            fontSize: 18,
								            fontFamily: 'Microsoft YaHei',
								            color:"#fff"


								        }
								    }
							},
							emphasis: {
								show: false,
								textStyle: {
									fontSize: '16',
									fontWeight: 'bold'
								}
							}
						},
						labelLine: {
							normal: {
								show: false
							}
						},
						data: seriesData.data
					}],
					color: ['#658ef0', '#ffb966', '#65dfa4', '#79c3ff', '#e479ff', '#ec9be0', '#f2f049', '#89f065']
				};
				var myChartPie = this.$echarts.init(document.getElementById(id));
				myChartPie.setOption(option);
			}
			},
		watch:{
			
		},
		created(){
			
		},
		mounted(){
			this.setEchartsPie(this.ids, this.data);
		},
		destoryed(){
			
		}
	}
</script>

<style>
</style>


